<template>
  <div>
    <el-tabs  v-model="activeName">
      <el-tab-pane label="" name="first">
        <el-table :data="tableData" style="width: 100%" id="table">
          <el-table-column prop="time" label="" width="180" />
          <el-table-column prop="title" label="" width="180" />
          <el-table-column prop="author" label="" width="180" />
          <el-table-column prop="body" label="" width="180"/>
          <el-table-column prop="file" label="" width="180"  #default="scope">
            <el-link  v-for="(f,key) in segmenteFile(scope.row.file)" :key="key" target="_blank" :href="f.fsrc" >{{f.fname}}</el-link>
          </el-table-column>
          <el-table-column prop="isemail" label="" width="80" />
          <el-table-column prop="operation" label="" width="180">
            <template  #default="scope">
              <!--
              <el-button type="primary" circle >
                <i class="el-icon-edit"></i>
              </el-button>
              -->
              <el-tooltip
                  effect="dark"
                  content=""
                  placement="top-start"
              >
                <el-button type="danger" circle @click="DelAnnounce(scope.$index)">
                  <i class="el-icon-delete"></i>
                </el-button>
              </el-tooltip>
              <el-tooltip
                  effect="dark"
                  content=""
                  placement="top-start"
              >
                <el-button type="success" circle @click="LookAnnounce(scope.$index)">
                  <i class="el-icon-view"></i>
                </el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <br />
        <template style="width: 100%">
          <div style="display:block;margin-left: 35%;">

          <el-pagination background layout="prev, pager, next" :total="pagenum"
                         :hide-on-single-page="false" :current-page="page" :page-size="5"
                         @current-change="showPage" >
          </el-pagination>
          </div>
        </template>


      </el-tab-pane>

      <el-tab-pane label="" name="second">
        <el-form :model="param" :rules="rules" ref="announce" label-width="80px">
          <el-form-item label="" prop="time">
            <el-input type="text" placeholder="xxxx-xx-xx xx:xx:xx" size="small"
                      prefix-icon="el-icon-time"
                      v-model="param.time"></el-input>
          </el-form-item>
          <el-form-item label="" prop="title">
            <el-input type="text" placeholder="" size="small"
                      prefix-icon="el-icon-connection"
                      v-model="param.title"></el-input>


          </el-form-item>
          <el-form-item label="" prop="author">
            <el-input type="text" placeholder="" size="small"
                      prefix-icon="el-icon-s-promotion"
                      v-model="param.author"></el-input>
          </el-form-item>
          <el-form-item label="" prop="body">
            <el-input placeholder="" size="small"
                      prefix-icon="el-icon-printer"
                      :autosize="{ minRows: 9, maxRows: 15 }"
                      type="textarea"
                      v-model="param.body"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-upload
                ref="upload"
                list-type="text"
                :on-preview="handlePictureCardPreview"
                :before-upload="beforeUpload"
                :on-remove="handleRemove"
                :auto-upload="false"
                :multiple="true"
                :limit="3"
                show-file-list
            >
              <el-button type="primary" ></el-button>
              <!--<el-button type="primary"></el-button>-->
              <template #tip>
                <div class="el-upload__tip">
                  500KB
                </div>
              </template>
            </el-upload>
          </el-form-item>

          <el-form-item prop="isEmail">
            <el-checkbox v-model="param.isemail" label="" size="large"></el-checkbox>
          </el-form-item>
          &nbsp;&nbsp;&nbsp;&nbsp;<el-button type="danger" @click="Submit"></el-button>
        </el-form>

      </el-tab-pane>
    </el-tabs>

  </div>
</template>

<script>
import ElementUI from "element-ui";

export default {
  //
  name: "AdminAnnounce",
  data(){
    return {
      pagenum:0,
      page:1,
      //
      activeName:"first",
      //
      tableData : [],
      fileList:[],
      form:"",
      //
      param:{
        time:"",
        title:"",
        author:"",
        body:"",
        isemail:false,
      },
      //
      rules: {
        time: [
          {required: true, message: "", trigger: "blur"}
        ],
        title: [
          {required: true, message: "", trigger: "blur"}
        ],
        author:[
          {required: true, message: "", trigger: "blur"}
        ],
        body: [
          {required: true, message: "", trigger: "blur"}
        ],
      }
    }
  },
  created() {
    // 
    this.$public.tokenadminlogin2(this.$cookies,this.axios,this.$store,this.$router,this.$message)
  },
  mounted(){
    //this.GetAnnounce()
    this.showPage(1);
    this.calcPage()
    this.form = new FormData();         //
  },
  methods:{
    //
    calcPage(){
        let params = new URLSearchParams()
        params.append("token",this.$cookies.get("token"))
        this.axios.post("announcenum",params
        ).then((response) => {
          //console.log(response.data)
          if (response.data['msg'] == "ok") {
            this.pagenum = parseInt(response.data['num'])
          }
        })
    },
    //
    showPage(page){
      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)',
        target:'#table'
      });
      let params = new URLSearchParams()
      params.append("token",this.$cookies.get("token"))
      params.append("page",page)      //5
      this.axios.post("showsomeannounce",
          params
      ).then((response) => {
        load.close()
        //console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.tableData = []
          for( let i = 0 ; i < response.data["announce"].length ;  i++ ) {
            this.tableData.push({
              title: response.data["announce"][i]['title'],
              time: response.data["announce"][i]['time'],
              author: response.data["announce"][i]['author'],
              body: response.data["announce"][i]['body'].slice(0, 80) + "",      //100
              file: response.data["announce"][i]['file'],
              isemail: response.data["announce"][i]['isemail']
            })
          }
        }
      })
    },
    //
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    beforeUpload(file) {
      //console.log(file)

      this.fileList.push(file);

      let images = [...this.fileList];
      //console.log("images", images);

      //this.param.append("file1", images);

      images.forEach((img) => {
        this.form.append("file", img); // 
      });
      return false;
    },
    //(file)
    segmenteFile(file){
      if( file == null )  return null       //
      let array = file.split('|');
      //console.log(array)
      //console.log(array.length)
      let list = [];
      for(let i = 0 ; i < array.length ; i++ ){
        let names = array[i].split('_');
        list.push({'fname':names[1],'fsrc': this.$store.state.baseurl +array[i] + '?token='+this.$cookies.get("token") })
      }
      //console.log(list)
      return list;
    },
    //
    GetAnnounce(){
      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)',
        target:'#table'
      });
      let params = new URLSearchParams()
      params.append("token",this.$cookies.get("token"))
      this.axios.post("showannounce",
          params
      ).then((response) => {
        load.close()
        if (response.data['msg'] == "ok") {
          //this.tableData = response.data["announce"]

          this.tableData = []
          for( let i = 0 ; i < response.data["announce"].length ;  i++ ) {
            this.tableData.push({
              title: response.data["announce"][i]['title'],
              time: response.data["announce"][i]['time'],
              author: response.data["announce"][i]['author'],
              body: response.data["announce"][i]['body'].slice(0, 80) + "",      //100
              file: response.data["announce"][i]['file'],
              isemail: response.data["announce"][i]['isemail']
            })
          }
          //console.log(this.tableData)
        }
      })
    },
    //
    DelAnnounce(index){
      //console.log(index+" "+this.tableData[index])
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //loading
        const load = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        let params = new URLSearchParams();
        params.append('time',this.tableData[index]['time']);
        params.append('title',this.tableData[index]['title']);
        params.append("token",this.$cookies.get("token"))
        this.axios.post("deleteannounce",
            params,
        ).then((response) => {
          load.close()
          //console.log(response.data)
          if (response.data['msg'] == "ok") {
            this.tableData = response.data["announce"]
            this.GetAnnounce();
            this.$forceUpdate()         //
          }
        })
        this.$message({
          type: 'success',
          message: '!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    //
    LookAnnounce(index){
      let time = this.tableData[index]['time']
      let title = this.tableData[index]['title']
      this.$router.push("/announcement?title="+title+"&time="+time+"&token="+this.$cookies.get("token"));
    },
    Submit(){
      let error = 0;
      this.$refs.announce.validate((valid) => {
        if (!valid) {
          this.$message({
            type: 'error',
            message: ''
          });
          error = 1;
          return;
        } else {
          //
        }
      });
      if( error == 1 )  return;

      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //loading
        const load = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        //axiosnull
        //let params = new URLSearchParams()
        this.$refs.upload.submit();

        this.form.append("token",this.$cookies.get("token"))
        this.form.append('title', this.param.title);
        this.form.append('time', this.param.time);
        this.form.append('author', this.param.author);
        this.form.append('body', this.param.body);
        this.form.append('isemail', this.param.isemail);
        //console.log(this.form)
        //    
        let configs = {
          headers: { "Content-Type": "multipart/form-data" }
        };

        this.axios.post("addannounce",
            this.form,configs
        ).then((response) => {
          load.close()
          //console.log(response.data)
          //console.log(response.data['msg'])
          if( response.data['msg'] == "ok"){
            ElementUI.Message({
              message: '',
              type: 'success'
            });
            //this.GetCode();
          }else {
            ElementUI.Message({
              message: '',
              type: 'error'
            });
          }
        })
      }).catch((error) => {

        console.log(error)
        this.$message({
          type: 'info',
          message: ''
        });
      });

    }
  },
}
</script>

<style scoped>
.el-input{
  width:320px;
}
</style>