<template>
  <div>
    <h1></h1>
    <el-table :data="codeData" style="width:540px " id="table">
      <el-table-column prop="name" label="" width="180"  />
      <el-table-column prop="code" label="" width="180" />
      <el-table-column prop="operation" label="" width="180">
        <template  #default="scope">
          <el-button type="primary" circle @click="editCode(scope.$index)" >
            <i class="el-icon-edit"></i>
          </el-button>
          <el-button type="danger" icon="Delete" circle @click="deleteCode(scope.$index)">
            <i class="el-icon-delete"></i>
          </el-button>
        </template>
      </el-table-column>

    </el-table>

    <el-divider></el-divider>
    <h1></h1>
    <el-form :model="param" :rules="rules" ref="addcode" label-width="80px">

      <el-form-item label="" prop="code">
        <el-input type="text" placeholder="" size="small"
                  prefix-icon="el-icon-notebook-1"
                  v-model="param.code" style="width: 200px;"></el-input>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input type="text" placeholder="" size="small"
                  prefix-icon="el-icon-lollipop"
                  v-model="param.name" style="width: 200px;"></el-input>
      </el-form-item>
      &nbsp;&nbsp;&nbsp;<el-button type="danger" @click="Submit"></el-button>
    </el-form>

    <el-dialog :visible="dialogFormVisible" title=""
               @close="dialogFormVisible=false"
               :close-on-click-modal="true" :append-to-body="true">
      <el-form :model="form" :rules="rules2" ref="modifycode">
        <el-form-item label="" label-width="140px" prop="code">
          <el-input v-model="form.code"  prefix-icon="el-icon-notebook-1" autocomplete="off" />
        </el-form-item>
        <el-form-item label="" label-width="140px" prop="name">
          <el-input v-model="form.name"  prefix-icon="el-icon-lollipop" autocomplete="off" />
        </el-form-item>

      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogFormVisible = false"></el-button>
        <el-button type="primary" @click="modifyCode"></el-button>
      </span>
      </template>
    </el-dialog>

  </div>
</template>

<script>
import ElementUI from "element-ui";

export default {
  //
  name: "AdminManage",
  data(){
    return {
      //
      dialogFormVisible:false,
      param:{
        code:"",
        name:""
      },
      // 
      form:{
        code:"",
        name:""
      },
      index:-1,
      //
      codeData:[ ],
      //
      rules: {
        code: [
          {required: true, message: "", trigger: "blur"},
        ],
        name: [
          {required: true, message: "", trigger: "blur"}
        ],
      },
      //
      rules2: {
        code: [
          {required: true, message: "", trigger: "blur"},
        ],
        name: [
          {required: true, message: "", trigger: "blur"}
        ],
      }
    }
  },
  created() {
    // 
    this.$public.tokenadminlogin2(this.$cookies,this.axios,this.$store,this.$router,this.$message)
  },
  mounted(){
      this.GetCode();
      this.$forceUpdate()         //
  },
  methods:{
    //
    GetCode(){
      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)',
        target:'#table'
      });
      let params = new URLSearchParams()
      params.append("token",this.$cookies.get("token"))
      //console.log("" )
      this.axios.post("showcode",params
      ).then((response) => {
        load.close()
        //console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.codeData = response.data["course"]
        }
      })
    },
    //
    modifyCode(){
      let error = 0;
      this.$refs.modifycode.validate((valid) => {
        if (!valid) {
          this.$message({
            type: 'error',
            message: ''
          });
          error = 1;
          return;
        } else {
          //
        }
      });
      if( error == 1 )  return;


      this.dialogFormVisible = false;
      let index = this.index
      if( this.codeData[index]['code'] ==  this.form.code && this.codeData[index]['name'] ==  this.form.name ){
        //
        this.$message({
          type: 'error',
          message: ''
        });
        return;
      }
      //console.log(index+" "+this.codeData[index]['code'])
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //loading
        const load = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        let params = new URLSearchParams()
        params.append("token",this.$cookies.get("token"))
        params.append('oldcode',this.codeData[index]['code']);
        params.append('oldname',this.codeData[index]['name']);
        params.append('newcode',this.form.code);
        params.append('newname',this.form.name);

        this.axios.post("modifycode",
            params
        ).then((response) => {
          load.close()
          //console.log(response.data)
          if (response.data['msg'] == "ok") {
            this.GetCode();             //
            this.$forceUpdate()         //
          }
        })
        this.$message({
          type: 'success',
          message: '!'
        });
        this.GetCode();
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    //
    editCode(index){
      this.index = index

      this.dialogFormVisible = true;
      this.form.code = this.codeData[index]['code']
      this.form.name = this.codeData[index]['name']
      this.$forceUpdate()         //

    },
    //
    deleteCode(index){

      //console.log(index+" "+this.codeData[index]['code'])
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {

        let params = new URLSearchParams()
        params.append("token",this.$cookies.get("token"))
        params.append('code',this.codeData[index]['code']);
        params.append('name',this.codeData[index]['name']);
        this.axios.post("deletecode",
            params
        ).then((response) => {
          //console.log(response.data)
          if (response.data['msg'] == "ok") {

            this.codeData = response.data["course"]
            this.GetCode();
            this.$forceUpdate()         //
          }
        })
        this.$message({
          type: 'success',
          message: '!'
        });
        this.GetCode();
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });

    },
    //
    Submit(){
      let error = 0;
      this.$refs.addcode.validate((valid) => {
        if (!valid) {
          this.$message({
            type: 'error',
            message: ''
          });
          error = 1;
          return;
        } else {
          //
        }
      });
      if( error == 1 )  return;

      this.$confirm(''+this.param.code+""+this.param.name+"", '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //loading
        const load = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        //axiosnull
        let params = new URLSearchParams()
        params.append("token",this.$cookies.get("token"))
        params.append('code', this.param.code);
        params.append('name', this.param.name);
        //console.log(params)
        this.axios.post("addcode",
            params
        ).then((response) => {
          load.close()
          //console.log(response.data)
          //console.log(response.data['msg'])
          if( response.data['msg'] == "ok"){
            ElementUI.Message({
              message: '',
              type: 'success'
            })
            this.$forceUpdate()
            this.GetCode();
          }else {
            ElementUI.Message({
              message: '',
              type: 'error'
            });
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });


    },


  }

}
</script>

<style scoped>

</style>