import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    // 

    state: {
        baseurl:"https://orall.top/spring_new_war",
        //baseurl: "http://8.134.142.145:8080/kaoyitong",
        username:"",                //
        //
        user: {
            username: '',
            name:'',
        },
        //
        adminuser:{
            username:"",
            permission:"",
        }
    },
    // mutations
    // mutations
    mutations: {
        //
        forget(state,payload){
            state.username = payload
        },
        //
        forgetSuccess(state){
            state.username = ""
        },
        //
        login (state, payload) {
            //console.log("asdasd" + payload)
            state.user = payload
        },
        //user
        logout (state) {
            state.user = {
                    username: '',
                    email: '',
                    name:'',
                }
        },
        //
        adminlogin (state, payload) {
            //console.log("asdasd" + payload)
            state.adminuser = payload
        },
        //user
        adminlogout (state) {
            state.adminuser = {
                username:"",
                permission:"",
            }
        }
    },
    // actions
    // actionscommitmutation
    actions: {
        login (context, payload) {
            context.commit('login', payload)
        },
        logout (context) {
            context.commit('logout')
        }
    }
})

export default store
