<template>
  <div style="height: 100vh">
    <el-container>
      <el-header>
        <Header></Header>
      </el-header>
      <el-container>
        <el-main>
          <!--
          prop model  validateresetFields 
          -->
          <!-- direction="vertical"-->

          <el-col :span="12">
            <div class="table">
              <div class="sontable">
                <h1>/</h1>
                <el-divider></el-divider>
                <el-form :model="param" :rules="rules" ref="forget" label-width="80px">
                  <el-form-item label="" prop="username">
                    <el-input type="text" placeholder="" size="small"
                              prefix-icon="el-icon-user"
                              v-model="param.username"></el-input>
                  </el-form-item>
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                  &nbsp;&nbsp;&nbsp;<el-button type="primary" @click="Submit"></el-button>
                </el-form>
              </div>
            </div>
          </el-col>
          <el-col :span="12">
            <div style="margin-left: 10px;margin-top: 100px">
              <el-card>
                <span></span>
                <p>
                  <br/>
                  <br/>5. </p>
              </el-card>
            </div>
          </el-col>
        </el-main>
      </el-container>
      <el-foot>
        <Footer></Footer>
      </el-foot>
    </el-container>
  </div>
</template>

<script>
import Header from "@/components/Header";
import Footer from "@/components/Footer";
import ElementUI from "element-ui";
export default {
  name: "Forget",
  components: {Header,Footer},
  data(){
    return {
      param:{
        username:"",
        email:"",
        name:"",
        sex:"",
        password:"",
        qrpassword:"",
        ymz:""
      },
      //
      rules:{
        username:[
          { required:true,message:"",trigger:["blur","change"]},
          { pattern: /^[\u4e00-\u9fa5a-zA-Z0-9]+$/,message: '',trigger:["blur","change"] },
          { pattern: new RegExp(/^\S*$/, 'g'), message: '',trigger:["blur","change"] }
        ],
      }
    }

  },
  methods:{
    Submit(){
      let error = 0;
      this.$refs.forget.validate((valid) => {
        if (!valid) {
          //console.log("As")
          this.$message({
            type: 'error',
            message: ''
          });
          error = 1;
          return;
        } else {
          //
        }
      });
      if( error == 1 )  return;

      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      let params = new URLSearchParams();
      params.append('username', this.param.username);

      this.axios({
        method: 'post',
        url:"forgetpass",
        withCredentials:true,
        data:params,
        dataType:'JSONP',  // Ajax,
      }).then((response) => {
        //console.log(response.data)
        //console.log(response.data['msg'])
        load.close()
        if( response.data['msg'] == "ok"){
          // user
          this.$store.commit('forget',this.param.username)
          ElementUI.Message({
            message: '',
            type: 'success'
          });

          this.$router.push("/forgetpass")

        }else{          //
          //this.error_msg = response.data['error']
          ElementUI.Message({
            message: '' + response.data['error']+",5",
            type: 'error'
          });

          setTimeout(
              //5
              this.goHome
              , 5000);

        }
      })




    },
    ///
    goHome(){
      this.$router.push("/");
    },
    //
    goRegister(){
      this.$router.push("/register");
    },
    //
    goForget(){
      this.$router.push("/");
    },
  }
}
</script>

<style lang="less" scoped>
/**/
.table{
  margin-top:20px;
  margin-left: 20px;
  width: 600px;
  font-size:14px;
  line-height:34px;
}
.sontable{
  margin-top:100px;
  margin-left:300px;
}
.el-card{
  width: 270px;
  height: 320px;
  margin-top:100px;
  margin-left:150px;
  span{
    font-weight: bolder;
    font-size: 17px;
  }
  p{
    margin-top:20px;
    font-size:14px;
    color:#424242;
    line-height:34px;
  }
}
.el-input{
  width:240px;
}
</style>