<template>
  <div>
    <el-form :model="param" ref="find" label-width="80px">
      <el-form-item label="" prop="find">

        &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;<el-input type="text" placeholder="" size="small"
                  prefix-icon="el-icon-search"
                  v-model="param.find" style="width: 350px"></el-input>
        &nbsp; &nbsp;
        <el-button type="primary" @click="Search">
          <i class="el-icon-search"></i>
          
        </el-button>
        <el-collapse accordion style="width: 500px;margin-left: 60px;white-space: pre-wrap;">
          <el-collapse-item title="   " name="1">
          <el-card style="width: 540px">
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <el-button style="margin-right: 1px" type="danger" size="small" @click="cancel"></el-button>
            <div>
              <span></span>&nbsp;&nbsp;&nbsp;
              <br/>&nbsp;
              <el-radio-group v-model="tagchoose.content.which" size="small" @change="toTag">
                <el-radio-button label="" @click.native.prevent="radioClick('',1)"/>
                <el-radio-button label="" @click.native.prevent="radioClick('',1)" />
                <el-radio-button label="" @click.native.prevent="radioClick('',1)"/>
                <el-radio-button label="" @click.native.prevent="radioClick('',1)"/>
                <el-radio-button label="" @click.native.prevent="radioClick('',1)"/>
              </el-radio-group>
            </div>
            <div>
              <span></span>&nbsp;&nbsp;&nbsp;
              <br/>&nbsp;
              <el-radio-group v-model="tagchoose.sex.which" size="small"  >
                <el-radio-button label=""  @click.native.prevent="radioClick('',2)"/>
                <el-radio-button label="" @click.native.prevent="radioClick('',2)"/>
              </el-radio-group>
            </div>
            <div>
              <span></span>&nbsp;&nbsp;&nbsp;
              <br/>&nbsp;
              <el-radio-group v-model="tagchoose.zycc.which" size="small"  @change="toTag">
                <el-radio-button label="" @click.native.prevent="radioClick('',3)" />
                <el-radio-button label="" @click.native.prevent="radioClick('',3)"/>
              </el-radio-group>
            </div>
            <div>
              <span></span>&nbsp;&nbsp;&nbsp;
              <br/>&nbsp;
              <el-radio-group v-model="tagchoose.zymc.which" size="small"  @change="toTag">
                <el-radio-button label="" @click.native.prevent="radioClick('',4)" :disabled="zy" />
                <el-radio-button label="" @click.native.prevent="radioClick('',4)" :disabled="zy"/>
                <el-radio-button label="" @click.native.prevent="radioClick('',4)" :disabled="bk"/>
                <el-radio-button label="" @click.native.prevent="radioClick('',4)" :disabled="bk"/>
              </el-radio-group>
            </div>

            <div>
              <span></span>&nbsp;&nbsp;&nbsp;
              <br/>&nbsp;
              <el-radio-group v-model="tagchoose.res.which" size="small"  @change="toTag">
                <el-radio-button label=""  @click.native.prevent="radioClick('',5)"/>
                <el-radio-button label="" @click.native.prevent="radioClick('',5)"/>
                <el-radio-button label="" @click.native.prevent="radioClick('',5)" />
              </el-radio-group>
            </div>
          </el-card>
            <br>
            <el-tag
                v-for="tag in tags"
                :key="tag.name"
                class="mx-1"
                closable
                @close="handleClose(tag)"
                :type="tag.type"
            >
              {{ tag.name }}
            </el-tag>
          </el-collapse-item>
        </el-collapse>
        </el-form-item>


      <!--
          <el-tag
              v-for="tag in dynamicTags"
              :key="tag"
              class="mx-1"
              closable
              :disable-transitions="false"
              @close="handleClose(tag)"
          >
            {{ tag }}
          </el-tag>
          <el-input
              v-if="inputVisible"
              ref="InputRef"
              v-model="inputValue"
              class="ml-1 w-20"
              size="small"
              @keyup.enter="handleInputConfirm"
              @blur="handleInputConfirm"
          >
          </el-input>
        -->
    </el-form>
    <div style="margin-left: 65%">
      <el-tooltip
          effect="dark"
          content=""
          placement="top">
        <el-button type="primary" @click="AllpassSign">
          
        </el-button>
      </el-tooltip>
      <el-tooltip
          effect="dark"
          content=""
          placement="top">
        <el-button type="primary"  @click="AllrefuseSign"></el-button>
      </el-tooltip>
      <el-button type="primary" @click="exportExcel"></el-button>
    </div>
    <!--    -->
    <el-table :data="outData"  class="outtable"  style="display:none">
      <el-table-column prop="name" label="" width="70" />
      <el-table-column prop="sex" label="" width="50" />
      <el-table-column prop="sfzh" label="" width="100" />
      <el-table-column prop="ticket" label="" width="100" />
      <el-table-column prop="tele" label="" width="100" />
      <el-table-column prop="email" label="" width="100" />
      <el-table-column prop="zycc" label="" width="100" />
      <el-table-column prop="zymc" label="" width="100" />
      <el-table-column prop="kcdm" label="" width="100" />
      <el-table-column prop="file" label="" width="100" #default="scope" >
        <el-link v-for="(f,key) in segmenteFile(scope.row.file)" :key="key" target="_blank" :href="f.fsrc">{{f.fname}}</el-link>
      </el-table-column>
      <el-table-column prop="res" label="" width="100" />

    </el-table>

    <el-table :data="tableData" style="width: 100%" class="roomtable">
      <el-table-column prop="name" label="" width="70" />
      <el-table-column prop="sex" label="" width="50" />
      <el-table-column prop="sfzh" label="" width="100" />
      <el-table-column prop="ticket" label="" width="100" />
      <el-table-column prop="tele" label="" width="100" />
      <el-table-column prop="email" label="" width="100" />
      <el-table-column prop="zycc" label="" width="100" />
      <el-table-column prop="zymc" label="" width="100" />
      <el-table-column prop="kcdm" label="" width="100" />
      <el-table-column prop="file" label="" width="100" #default="scope" >
        <el-link v-for="(f,key) in segmenteFile(scope.row.file)" :key="key" target="_blank" :href="f.fsrc">{{f.fname}}</el-link>
      </el-table-column>
      <el-table-column prop="res" label="" width="100" />
      <el-table-column  label="" width="180" >
        <template #default="scope">
          <div v-if="tableData[scope.$index].res == '' ">
            <el-button type="primary" circle @click="PassSign(scope.$index)">
              <i class="el-icon-check"></i>
            </el-button>
            <el-button type="danger" circle @click="RefuseSign(scope.$index)">
              <i class="el-icon-close"></i>
            </el-button>
          </div>
          <div v-if="tableData[scope.$index].res == '' ">
            <el-button type="success" circle @click="BackSign(scope.$index)">
              <i class="el-icon-back"></i>
            </el-button>
          </div>
          <div v-if="tableData[scope.$index].res =='' ">
            <el-button type="success" circle @click="BackSign(scope.$index)">
              <i class="el-icon-back"></i>
            </el-button>
          </div>
        </template>



      </el-table-column>
    </el-table>
    <br />
    <template>
      <div style="display:block;margin-left: 35%;">
      <el-pagination background layout="prev, pager, next" :total="pagenum"
                     :hide-on-single-page="false" :current-page="page"
                     @current-change="showPage" >
      </el-pagination>
      </div>
    </template>

  </div>
</template>

<script>

export default {
  //
  name: "AdminExamine",
  data(){
    return {
      //
      pagenum:1,
      //
      page:1,
      //
      tags :[
      //  { name: 'Tag 1', type: '' },
      ],
      //
      bk:false,
      zy:false,
      //
      tagchoose:{
        content:{
          which:"0",    //0
        },
        sex:{
          which:"0",    //0
        },
        zycc:{
          which:"0",    //0
        },
        zymc:{
          which:"0",    //0
        },
        res:{
          which:"0",    //0
        }
      },
      param: {
        find: "",
      },
      //
      type:1,
      tableData : [],
      outData:[]
    }
  },
  created() {
    // 
    this.$public.tokenadminlogin2(this.$cookies,this.axios,this.$store,this.$router,this.$message)
  },
  mounted() {
    this.calcPage();
    //
    this.showPage(this.page);
    this.$forceUpdate()         //
   // this.out()
  },
  methods:{
    //
    cancel(){
      this.type = 1;
      this.tagchoose.content.which = "0";
      this.tagchoose.sex.which = "0";
      this.tagchoose.zycc.which = "0";
      this.tagchoose.zymc.which = "0";
      this.tagchoose.res.which = "0";

      this.tags = []

      this.calcPage();
      //
      this.showPage(this.page);
      this.$forceUpdate()         //
    },
    //
    out(){        //https://www.cnblogs.com/xiaofeilin/p/14621205.html
      return new Promise(resolve => {
      if( this.type == 1 ){
        //loading
        const load = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        let params = new URLSearchParams()
        params.append("token",this.$cookies.get("token"))
        //params.append("page",page)
        //page
        this.axios.post("examine",
            params
        ).then((response) => {
          load.close()
          //console.log(response.data)
          if (response.data['msg'] == "ok") {
            this.outData = response.data['examine']
            resolve('ok')
          }
        })
      }else{
        //loading
        const load = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        let params = new URLSearchParams()
        params.append("token",this.$cookies.get("token"))
        //page
        //params.append("page","1")
        params.append("content",this.tagchoose.content.which)
        params.append("sex",this.tagchoose.sex.which)
        params.append("zycc",this.tagchoose.zycc.which)
        params.append("zymc",this.tagchoose.zymc.which)
        params.append("res",this.tagchoose.res.which)
        params.append("search",this.param.find)
        this.axios.post("search",
            params
        ).then((response) => {
          load.close()
          //console.log(response.data)
          if (response.data['msg'] == "ok") {
            this.outData = response.data['examine']
            resolve('ok')
          }
        })
      }
      })
    },
    //
    exportExcel(){
      this.out().then(res=>{
        console.log(res)
        var wb = this.$xlsx.utils.table_to_book(document.querySelector(".outtable"),{raw:true});
        /* get binary string as output */
        var wbout = this.$xlsx.write(wb, {
          bookType: "xlsx",
          bookSST: true,
          type: "array",
        });
        try {
          let d = new Date()
          let s = d.getFullYear()+"."+d.getMonth()+"."+d.getDate()
          this.$fileSaver.saveAs(
              new Blob([wbout], { type: "application/octet-stream" }),
              "("+s+").xlsx"
          );
        } catch (e) {
          if (typeof console !== "undefined") console.log(e, wbout);
        }
        // 
        this.outData = []
      })
    },
    //
    handleClose(tag) {
      if( tag.name.search("") != -1){
        this.tagchoose.content.which = "0";
      }else if( tag.name.search("") != -1){
        this.tagchoose.sex.which = "0";
      }else if( tag.name.search("") != -1){
        this.tagchoose.zycc.which = "0";
        this.zy = this.bk = false
      }else if( tag.name.search("") != -1){
        this.tagchoose.zymc.which = "0";
      }else if( tag.name.search("") != -1){
        this.tagchoose.res.which = "0";
      }
      this.tags.splice( this.tags.indexOf(tag), 1);
    },
    //el-radoi https://blog.csdn.net/qwe19991123/article/details/122989049
    radioClick (e,model) {
      if( model == 1 ){
        e === this.tagchoose.content.which? (this.tagchoose.content.which = '0'):(this.tagchoose.content.which = e)
      }else if( model == 2 ){
        e === this.tagchoose.sex.which? (this.tagchoose.sex.which = '0'):(this.tagchoose.sex.which = e)
      }else if( model == 3 ){
        e === this.tagchoose.zycc.which? (this.tagchoose.zycc.which = '0'):(this.tagchoose.zycc.which = e)
        if( this.tagchoose.zycc.which == "" ){
          this.bk = true
        }else{
          this.bk = false
        }
        if( this.tagchoose.zycc.which == "" ){
          this.zy = true
        }else{
          this.zy = false
        }
      }else if( model == 4 ){
        e === this.tagchoose.zymc.which? (this.tagchoose.zymc.which = '0'):(this.tagchoose.zymc.which = e)
      }else if( model == 5 ){
        e === this.tagchoose.res.which? (this.tagchoose.res.which = '0'):(this.tagchoose.res.which = e)
      }
      this.toTag()
    },
    //
    calcPage(){
      if( this.type == 1 ){   //
        let params = new URLSearchParams()
        params.append("token",this.$cookies.get("token"))
        this.axios.post("signupnum",params
        ).then((response) => {
          //console.log(response.data)
          if (response.data['msg'] == "ok") {
            this.pagenum = parseInt(response.data['num'])
          }
        })
      }else{                //
        let params = new URLSearchParams()
        params.append("token",this.$cookies.get("token"))
        params.append("content",this.tagchoose.content.which)
        params.append("sex",this.tagchoose.sex.which)
        params.append("zycc",this.tagchoose.zycc.which)
        params.append("zymc",this.tagchoose.zymc.which)
        params.append("res",this.tagchoose.res.which)
        params.append("search",this.param.find)
        this.axios.post("searchnum",params
        ).then((response) => {
          //console.log(response.data)
          if (response.data['msg'] == "ok") {
            this.pagenum = parseInt(response.data['num'])
          }
        })
      }

    },
    //
    Search(){
      this.type = 2;
      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      let params = new URLSearchParams()
      params.append("token",this.$cookies.get("token"))
      params.append("page","1")
      params.append("content",this.tagchoose.content.which)
      params.append("sex",this.tagchoose.sex.which)
      params.append("zycc",this.tagchoose.zycc.which)
      params.append("zymc",this.tagchoose.zymc.which)
      params.append("res",this.tagchoose.res.which)
      params.append("search",this.param.find)
      this.axios.post("search",
          params
      ).then((response) => {
        load.close()
        //console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.tableData = response.data['examine']
          this.calcPage()
        }
      })
    },
    //
    showPage(page){
      if( this.type == 1 ){
        //loading
        const load = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)',
          target:'#table'
        });
        let params = new URLSearchParams()
        params.append("token",this.$cookies.get("token"))
        params.append("page",page)
        this.axios.post("examine",
            params
        ).then((response) => {
          load.close()
          //console.log(response.data)
          if (response.data['msg'] == "ok") {
            this.tableData = response.data['examine']
          }
        })
      }else{
        //loading
        const load = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)',
          target:'#table'
        });
        let params = new URLSearchParams()
        params.append("token",this.$cookies.get("token"))
        params.append("page",page)
        params.append("content",this.tagchoose.content.which)
        params.append("sex",this.tagchoose.sex.which)
        params.append("zycc",this.tagchoose.zycc.which)
        params.append("zymc",this.tagchoose.zymc.which)
        params.append("res",this.tagchoose.res.which)
        params.append("search",this.param.find)
        this.axios.post("search",
            params
        ).then((response) => {
          load.close()
          //console.log(response.data)
          if (response.data['msg'] == "ok") {
            this.tableData = response.data['examine']
          }
        })
      }

    },
    //(file)
    segmenteFile(file){
      if( file == null )  return null       //
      let array = file.split('|');
      //console.log(array)
      //console.log(array.length)
      let list = [];
      for(let i = 0 ; i < array.length ; i++ ){
        let names = array[i].split('_');
        list.push({'fname':names[1],'fsrc': this.$store.state.baseurl +array[i] + '?token='+this.$cookies.get("token") })
      }
      //console.log(list)
      return list;
    },
    //
    toTag(){
      //console.log(message)
      this.tags = []
      //console.log()
      if( this.tagchoose.content.which != "0" ){
        this.tags.push({ name: ""+this.tagchoose.content.which, type: '' })
      }
      if( this.tagchoose.sex.which != "0" ){
        this.tags.push({ name: ""+this.tagchoose.sex.which, type: '' })
      }
      if( this.tagchoose.zycc.which != "0" ){
        this.tags.push({ name: ""+this.tagchoose.zycc.which, type: '' })
      }
      if( this.tagchoose.zymc.which != "0" ){
        this.tags.push({ name: ""+this.tagchoose.zymc.which, type: '' })
      }
      if( this.tagchoose.res.which != "0" ){
        this.tags.push({ name: ""+this.tagchoose.res.which, type: '' })
      }
      //this.handleClose(message)
    },
    //
    PassSign(index){
      let params = new URLSearchParams()
      params.append("token",this.$cookies.get("token"))
      params.append("ticket",this.tableData[index]["ticket"])
      params.append("res","1")
      this.axios.post("auditstatus",
          params
      ).then((response) => {
        //console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.$forceUpdate()
          this.showPage(this.page)
          //console.log(response.data)
        }
      })
    },
    //
    RefuseSign(index){
      let params = new URLSearchParams()
      params.append("token",this.$cookies.get("token"))
      params.append("ticket",this.tableData[index]["ticket"])
      params.append("res","-1")
      this.axios.post("auditstatus",
          params
      ).then((response) => {
        //console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.$forceUpdate()
          //console.log(response.data)
          this.showPage(this.page)
        }
      })
    },
    //
    BackSign(index){
      let params = new URLSearchParams()
      params.append("token",this.$cookies.get("token"))
      params.append("ticket",this.tableData[index]["ticket"])
      params.append("res","0")
      this.axios.post("auditstatus",
          params
      ).then((response) => {
        //console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.$forceUpdate()
          //console.log(response.data)
          this.showPage(this.page)
        }
      })
    },
    //
    AllpassSign(){
      this.$confirm(
          ', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }
      )
          .then(() => {
            for(let i = 0 ; i < this.tableData.length ; i++ ){
              if( this.tableData[i]['res'] == "" ){
                this.PassSign(i);
              }
            }
            this.$message({
              type: 'success',
              message: '!'
            });

            this.showPage(this.page)
            this.$forceUpdate()
          })
          .catch(() => {
            //console.log(error)
            this.$message({
              type: 'info',
              message: ''
            });
          })

    },
    //
    AllrefuseSign(){
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '!'
        });
        for(let i = 0 ; i < this.tableData.length ; i++ ){
          if( this.tableData[i]['res'] == "" ){
            this.RefuseSign(i);
          }
        }
        this.showPage(this.page)
        this.$forceUpdate()

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
  }
}
</script>

<style scoped>

</style>