<template>
  <div>
    <div style="height: 100vh" >
      <el-container>
        <el-header>
          <Header></Header>
          <div style=" right: 76px;top: 55px; position: absolute;">
            <el-dropdown>
              <div>
                <el-avatar icon="el-icon-user">
                  <i class="el-icon-arrow-down"></i>
                </el-avatar>
                <div style="color: #FFFFFF;text-align: center" > {{ help.name }}</div>
              </div>

              <el-dropdown-menu>
                <el-dropdown-item @click.native="goPer"></el-dropdown-item>
                <el-dropdown-item @click.native="logout"></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-header>
        <el-container>
          <el-side width="200px" style="min-width:200px;max-width:200px;margin-top: 90px;">
            <el-menu
                default-openeds="['1']"
                background-color="#fff"
                text-color="#000000"
                active-text-color="#409EFF"
                :default-active="$route.path"
                router>
              <el-menu-item index="/adminsuccess/adminindex" >
                <i class="el-icon-house"></i>
                </el-menu-item>
              <el-submenu index="1">
                <template slot="title">
                  <i class="el-icon-menu"></i>
                  <span slot="title"></span>
                </template>
                <el-menu-item index="/adminsuccess/adminmanage">
                  <i class="el-icon-edit"></i>
                  </el-menu-item>
                <el-menu-item index="/adminsuccess/adminexamine">
                  <i class="el-icon-document-checked"></i>
                  </el-menu-item>
                <el-menu-item index="/adminsuccess/adminannounce">
                  <i class="el-icon-message"></i>
                  
                </el-menu-item>
                <el-menu-item @click="warn" >
                  <i class="el-icon-cpu"></i>
                  </el-menu-item>
                <el-menu-item index="/adminsuccess/adminjurisdict">
                  <i class="el-icon-lock"></i>
                  
                </el-menu-item>
              </el-submenu>
            </el-menu>
          </el-side>
          <el-main  style="margin-top: 90px">
            <!--  -->
            <router-view></router-view>
          </el-main>

        </el-container>
        <el-foot>
          <Footer></Footer>
        </el-foot>
      </el-container>
    </div>
  </div>
</template>

<script>

import Header from "@/components/Header";
import Footer from "@/components/Footer";
import ElementUI from "element-ui";

export default {
  name: "AdminSuccess",
  components: {Header, Footer},
  data(){
    return {
      // 
      help: {name: "", other: ""},      // 
    }
  },
  created() {
    // 
    this.$public.tokenadminlogin(this.$cookies,this.axios,this.$store,this.$router,this.$message,this.help)
  },
  methods:{
    //
    warn(){
      //console.log("")
      ElementUI.Message({
        message: '',
        type: 'error'
      });
    },
    //
    goPer(){
      if( this.$store.state.adminuser.permission != 1 ){
        ElementUI.Message({
          message: '',
          type: 'info'
        });
      }else{
        ElementUI.Message({

          message: ':',
          type: 'info'
        });
      }



    },
    //
    logout(){
      this.$store.commit('adminlogout')     //vuex
      this.$cookies.remove('token')     //cookietoken
      //
      this.$router.push("/adminlogin");
    },

  }
}
</script>

<style scoped>
@import "../public.css";

</style>