<template>
  <div>

    <div class="block">

      <h1></h1>
      <br />
      <el-table :data="timeData" style="width:520px" id="timetable">
        <el-table-column prop="begin" label="" width="180"  />
        <el-table-column prop="end" label="" width="180" />
        <el-table-column prop="operation" label="" width="150">
          <template>
            <el-button type="danger" circle @click="deleteTime" >
              <i class="el-icon-close"></i>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <br />
      <el-form :model="param"  :rules="rules2" ref="time" label-width="80px">
          <el-form-item label="" prop="begin">
            <el-date-picker
                v-model="param.begin"
                type="datetime"
                placeholder=
            >
            </el-date-picker>
          </el-form-item>

        &nbsp;
          <el-form-item label="" prop="end">

          <el-date-picker
              v-model="param.end"
              type="datetime"
              placeholder=""
          >
          </el-date-picker>
          </el-form-item>
        &nbsp;&nbsp;&nbsp;
        <el-button type="primary" @click="setTime"></el-button>
        &nbsp;
        <el-button type="primary"></el-button>
      </el-form>
      <el-divider></el-divider>

      <h1></h1>

      <el-table :data="tableData" style="width: 720px" id="admintable">
        <el-table-column prop="account" label="" width="180" />
        <el-table-column prop="permission" label="" width="180" />
        <el-table-column prop="note" label="" width="180"/>
        <el-table-column prop="operation" label="" width="180">
          <template  #default="scope">
            <!--
            <el-button type="primary" circle >
              <i class="el-icon-edit"></i>
            </el-button>
            -->
            <el-button type="danger" icon="Delete" circle @click="deleteAdmin(scope.$index)">
              <i class="el-icon-delete"></i>
            </el-button>
          </template>
        </el-table-column>

      </el-table>
      <br />
      &nbsp;&nbsp;
      <div style="width: 100%">
        <el-button type="primary" style="margin-right: 10px" @click="addAdmin"></el-button>
      </div>

      <el-divider></el-divider>


      <h1></h1>
      <el-input
          id="log"
          disabled="true"
          rows="9"
          type="textarea"
          placeholder=""
          v-model="log"
      />

      </div>
    <el-dialog :visible="dialogFormVisible" title=""
               @close="dialogFormVisible=false"
               :close-on-click-modal="true" :append-to-body="true">
      <el-form :model="form" :rules="rules" ref="addadmin" label-width="80px">
        <el-form-item label="" prop="username">
          <el-input type="text" placeholder="" size="small"
                    prefix-icon="el-icon-user"
                    v-model="form.username"></el-input>
        </el-form-item>

        <el-form-item label="" prop="password">
          <el-input type="password" placeholder="8" size="small" show-password prefix-icon="el-icon-lock"
                    v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="qrpassword">
          <el-input type="password" placeholder="" size="small" show-password prefix-icon="el-icon-lock"
                    v-model="form.qrpassword"></el-input>
        </el-form-item>

        <el-form-item label="" prop="permission">
          <el-select v-model="form.permission" placeholder="" size="large">
            <el-option
                label=""
                value="1"
            ></el-option>
            <el-option
                label=""
                value="2"
            ></el-option>
          </el-select>

        </el-form-item>

        <el-form-item label="" prop="note">
          <el-input type="text" placeholder="" size="small"
                    prefix-icon="el-icon-tickets"
                    v-model="form.note"></el-input>
        </el-form-item>

        <el-form-item label="" prop="username">
          <el-input type="password" placeholder="" size="small"
                    prefix-icon="el-icon-key" show-password
                    v-model="form.oldpassword"></el-input>
        </el-form-item>
      </el-form>

      <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogFormVisible = false"></el-button>
        <el-button type="primary" @click="SubmitAdd"></el-button>
      </span>
      </template>
    </el-dialog>

  </div>
</template>

<script>
import ElementUI from "element-ui";

export default {
  //
  name: "AdminJurisdict",
  data() {
    // 
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
        // password 
      } else if (value !== this.form.password) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    // 
    var validateTime = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
        // password 
      } else if ( this.param.begin > value ) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      //
      param:{
        begin:"",
        end:"",
      },
      //
      log:"",
      //
      timeData:[
        {
          begin:"",
          end:"",
        }
      ],
      time1: "",
      time2: "",
      form:{
        username:"",
        password:"",
        qrpassword:"",
        permission:"",
        note:"",
        oldpassword:"",
      },
      //
      rules:{
        username:[
          { required:true,message:"",trigger:["blur"]},
          { pattern: /^[\u4e00-\u9fa5a-zA-Z0-9]+$/,message: '',trigger:["blur","change"] },
        ],
        password:[
          {required:true,message:"",trigger:["blur"]},
          { min: 6, message: '6', trigger: ["blur","change"] },
          { pattern: new RegExp(/^\S*$/, 'g'), message: '',trigger:["blur","change"] }
        ],
        qrpassword:[
          // {required:true,message:"",trigger:"blur"},
          { required: true, validator: validatePass2, trigger: 'blur' }
        ],
        oldpassword:[
          {required:true,message:"",trigger:["blur"]},
          { min: 6, message: '6', trigger: ["blur","change"] },
          { pattern: new RegExp(/^\S*$/, 'g'), message: '',trigger:["blur","change"] }
        ],
      },
      //
      rules2:{
        begin:[
          { required:true,message:"",trigger:["blur"]},
        ],
        end:[
          { required: true, validator: validateTime, trigger: 'blur' }
          //{ required:true,message:"",trigger:["blur"]},

        ],
      },
      dialogFormVisible:false,
      tableData : []

    }
  },
  created() {
    // 
    this.$public.tokenadminlogin2(this.$cookies,this.axios,this.$store,this.$router,this.$message)
  },
  mounted() {
    this.getTime()
    this.showAdmin()
    this.showlog()
  },
  methods: {
    //
    deleteTime(){
      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      let params = new URLSearchParams();
      params.append("token",this.$cookies.get("token"));
      this.axios.post("deleteopentime",params
      ).then((response) => {
        load.close()
        //console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.timeData = [
            {
              begin:"",
              end:"",
            }
          ]
        }
      })
    },
    //
    getTime(){
      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)',
        target:'#timetable'
      });
      let params = new URLSearchParams();
      params.append("token",this.$cookies.get("token"))
      this.axios.post("getopentime",
          params
      ).then((response) => {
        load.close()
        //console.log(response.data)
        if (response.data['msg'] == "ok") {
          let data = [{
            begin:response.data["begin"],
            end:response.data["end"],
          }]
          this.timeData = data
        }
      })
    },
    //
    setTime(){
      let error = 0;
      this.$refs.time.validate((valid) => {
        if (!valid) {
          this.$message({
            type: 'error',
            message: ''
          });
          error = 1;
          return;
        } else {
          //
        }
      });
      if( error == 1 )  return;

      //this.param.begin.Format("yyyy-MM-dd hh:mm:ss")
      //(new Date()).Format("yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18
      this.$confirm(''+String(this.param.begin)+"~"+this.param.end, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //loading
        const load = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        let params = new URLSearchParams();
        params.append('begin',this.param.begin);
        params.append('end',this.param.end);
        params.append('token',this.$cookies.get("token"));
        this.axios.post("setopentime",
            params,
        ).then((response) => {
          load.close()
          //console.log(response.data)
          if (response.data['msg'] == "ok") {

            this.getTime()
            this.$forceUpdate()         //
          }
        })
        this.$message({
          type: 'success',
          message: '!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    //
    deleteAdmin(index) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //loading
        const load = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        let params = new URLSearchParams();
        params.append('token',this.$cookies.get("token"));
        params.append('delaccount',this.tableData[index]['account']);
        this.axios.post("deleteadmin",
            params,
        ).then((response) => {
          load.close()
          //console.log(response.data)
          if (response.data['msg'] == "ok") {
            this.showAdmin()
            this.$message({
              type: 'success',
              message: '!'
            });
          }else{
            this.$message({
              type: 'error',
              message: ''
            });
          }
        })

      }).catch(() => {

      });
    },
    addAdmin(){
      //1
      if( this.$store.state.adminuser.permission != 1 ){
        ElementUI.Message({
          message: '',
          type: 'error'
        });
      }else{
        this.dialogFormVisible = true
      }
    },
    showlog(){
      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)',
        target:'#log'
      });
      let params = new URLSearchParams();
      params.append("token",this.$cookies.get("token"))
      this.axios.post("showlog",
          params
      ).then((response) => {
        load.close()
        //console.log(response.data)
        if (response.data['msg'] == "ok") {
          //lengthsize
          for( let i = 0 ; i < response.data['log'].length ; i++){

            this.log += response.data['log'][i] + "\n"
            this.$forceUpdate()         //
            //console.log(this.log)
          }
        }
      })
    },
    //
    SubmitAdd(){
      let error = 0;
      this.$refs.addadmin.validate((valid) => {
        if (!valid) {
          this.$message({
            type: 'error',
            message: ''
          });
          error = 1;
          return;
        } else {
          //
        }
      });
      if( error == 1 )  return;

      let params = new URLSearchParams()
      /*
      * token
        newusername
        newpassword
        permission
        oldpassword
        note
      * */
      //params.append("oldusername",this.$store.state.adminuser.username)
      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      params.append("oldpassword",this.form.oldpassword)
      params.append("newusername",this.form.username)
      params.append("newpassword",this.form.password)
      params.append("permission",this.form.permission)
      params.append("note",this.form.note)
      params.append('token',this.$cookies.get("token"));

      this.axios.post("addadmin",params
      ).then((response) => {
        load.close()

        if( response.data['msg'] == "ok"){
          this.$message({
            type: 'success',
            message: '!'
          });
          this.showAdmin()
          this.$forceUpdate()         //
        }else{          //
          this.$message({
            type: 'error',
            message: ',' + response.data['error'] +'!'
          });
        }
      })
    },

    //
    showAdmin(){
      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)',
        target:'#admintable'
      });
      //1
      let params = new URLSearchParams()
      params.append("token",this.$cookies.get("token"))
      this.axios.post("showadmin",
          params
      ).then((response) => {
        load.close()
        //console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.tableData = response.data["admin"]
        }
      })
    },
  }
}
</script>

<style scoped>
.h1{
  font-size: 25px;
}
</style>