<template>
  <div >
    <template>
      <el-steps :active="steps" style="width: 800px;margin-left: 10%">
        <el-step title="1" description=""></el-step>
        <el-step title="2" description=""></el-step>
        <el-step title="3" description=""></el-step>
      </el-steps>
      <div v-show="steps==1" style="margin-left: 30%;margin-top: 100px">

        <el-card style="width: 300px;">
          <h1 style="text-align: center"></h1>
          &nbsp;
         <el-button type="danger" @click='goSignUp'></el-button>
        </el-card>
      </div>
      <div v-show="steps==2" style="margin-left: 25%;margin-top: 100px">
        <template>
          <el-card style="width: 420px;">
          <el-descriptions title="" style="width: 380px;"
                           width="3px"
                           :column="1"
                           size="large"
                           label-align="center"
                           align="center"
                           border>
            <el-descriptions-item label="">{{infoData.name}}</el-descriptions-item>
            <el-descriptions-item label="">{{infoData.sex}}</el-descriptions-item>
            <el-descriptions-item label="">{{infoData.sfzh}}</el-descriptions-item>
            <el-descriptions-item label="">{{infoData.ticket}}</el-descriptions-item>
            <el-descriptions-item label="">{{infoData.tele}}</el-descriptions-item>
            <el-descriptions-item label="">{{infoData.zycc}}</el-descriptions-item>
            <el-descriptions-item label="">{{infoData.zymc}}</el-descriptions-item>
            <el-descriptions-item label="">{{infoData.kcdm}}</el-descriptions-item>
            <el-descriptions-item label="">
              <el-link v-for="(f,key) in segmenteFile(infoData.file)" :key="key" target="_blank" :href="f.fsrc">{{f.fname}}</el-link>
            </el-descriptions-item>
          </el-descriptions>
          <el-button type="danger" style="margin-left: 30%;margin-top: 20px" @click="modifySign"></el-button>
          </el-card>
        </template>
      </div>

      <el-dialog :visible="dialogFormVisible" title=""
                 @close="dialogFormVisible = false"
                 :close-on-click-modal="true" :append-to-body="true">
        <el-form :model="form" :rules="rules" ref="modifysignup" label-width="80px">
          <el-form-item label="" prop="name">
            <el-input type="text" placeholder="" size="small"
                      prefix-icon="el-icon-s-ticket"
                      v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="sex">
            <div>
              <el-radio v-model="form.sex" label="1" size="large"></el-radio>
              <el-radio v-model="form.sex" label="2" size="large"></el-radio>
            </div>
          </el-form-item>
          <el-form-item label="" prop="sfzh">
            <el-input type="text" placeholder="X" size="small"
                      prefix-icon="el-icon-postcard"
                      v-model="form.sfzh"></el-input>
          </el-form-item>

          <el-form-item label="" prop="ticket">
            <el-input type="text" placeholder="" size="small"
                      prefix-icon="el-icon-tickets"
                      v-model="form.ticket" disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="tele">
            <el-input type="text" placeholder="" size="small"
                      prefix-icon="el-icon-phone"
                      v-model="form.tele"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input type="text" placeholder="" size="small"
                      prefix-icon="el-icon-message"
                      v-model="form.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="zycc">
            <el-select v-model="form.zycc" placeholder="Select" size="small" @change="Change">
              <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" prop="zymc">
            <el-select v-model="form.zymc" placeholder="Select" size="small">
              <el-option
                  v-for="item in options_zymc"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" prop="kcdm">
            <el-checkbox-group v-model="form.kcdm">
              <el-checkbox  v-for="item in kcdm"
                            :label="item.name+'('+item.code+')'"
                            :key="item.code"
              >
              </el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-tag style="color:red"></el-tag>
          <el-form-item label="" prop="file">

            <el-upload
                ref="upload"
                list-type="text"
                :on-preview="handlePictureCardPreview"
                :before-upload="beforeUpload"
                :on-remove="handleRemove"
                :auto-upload="false"
                :multiple="true"
                :limit="3"
                show-file-list="true"
            >
              <el-button type="primary"></el-button>
              <!--<el-button type="primary"></el-button>-->
              <template #tip>
                <div class="el-upload__tip">
                  JPG/PNG/PDF500KB
                </div>
              </template>
            </el-upload>
          </el-form-item>


        </el-form>
        <template #footer>
          <span class="dialog-footer">
            <el-button @click="diaCancel"></el-button>
            <el-button type="primary" @click="SubmitModify"></el-button>
          </span>
        </template>
      </el-dialog>



      <div v-show="steps==3" style="margin-left: 30%;margin-top: 100px">
        <el-card style="width: 300px;">
          <h1 style="text-align: center"></h1>
          &nbsp;
          <span v-show="result==1">
            
          </span>
          <span v-show="result==-1">
            

          <el-button type="danger" @click='regoSignUp'></el-button>
          </span>
        </el-card>

      </div>

    </template>

  </div>
</template>

<script>
export default {
  //
  name: "RegisterStatus",
  data(){
    var validateIdNo = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
        return
      }
      var format = /^(([1][1-5])|([2][1-3])|([3][1-7])|([4][1-6])|([5][0-4])|([6][1-5])|([7][1])|([8][1-2]))\d{4}(([1][9]\d{2})|([2]\d{3}))(([0][1-9])|([1][0-2]))(([0][1-9])|([1-2][0-9])|([3][0-1]))\d{3}[0-9xX]$/;
      //
      if (!format.test(value)) {
        callback(new Error(''));
      }
      //
      //  1900;
      var year = value.substr(6, 4),//
          month = value.substr(10, 2),//
          date = value.substr(12, 2),//
          time = Date.parse(month + '-' + date + '-' + year),//date
          now_time = Date.parse(new Date()),//
          dates = (new Date(year, month, 0)).getDate();//
      if (time > now_time || date > dates) {
        callback(new Error(''));
      }
      //
      var c = new Array(7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2);  //
      var b = new Array('1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2'); //
      var id_array = value.split("");
      var sum = 0;
      for (var k = 0; k < 17; k++) {
        sum += parseInt(id_array[k]) * parseInt(c[k]);
      }
      if (id_array[17].toUpperCase() != b[sum % 11].toUpperCase()) {
        callback(new Error(''));
      }
      callback();
    }
    return {
      //
      dialogFormVisible:false,
      options_zymc: [],

      kcdm:[],
      param: "",  // 
      //
      form:{
        name:"",
        sex:"",
        sfzh:"",
        ticket:"",
        tele:"",
        email:"",
        zycc:"",
        zymc:"",
        kcdm:[],
      },
      steps:"1",
      //
      rules:{
        name:[
          {required:true,message:"",trigger:"blur"},
          { pattern: /^[\u4E00-\u9FA5]+$/i,message: '',trigger:["blur","change"] },
        ],
        sex:[
          {required:true,message:"",trigger:"blur"}
        ],
        sfzh:[
          {required:true,validator: validateIdNo,trigger:"blur"}
        ],
        ticket:[
          {required:true,message:"",trigger:"blur"},
          { pattern: /^[\u4e00-\u9fa5a-zA-Z0-9]+$/,message: '',trigger:["blur","change"] },
          { pattern: new RegExp(/^\S*$/, 'g'), message: '',trigger:["blur","change"] }

        ],
        tele:[
          { pattern: new RegExp(/^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/,'g'),
            message: '',trigger:["blur","change"]}
        ],
        email:[
          {type:"email",message:"",trigger:"blur"},
          {required:true,message:"",trigger:["blur","change"] }
        ],
        zycc:[
          {required:true,message:"",trigger:"blur"}
        ],
        zymc:[
          {required:true,message:"",trigger:"blur"}
        ],
      },
      options: [
        {
          value: '',
          label: '',
        },
        {
          value: '',
          label: '',
        },
      ],
      fileList: [],
      //1-1
      result:"1",
      //
      infoData:{
          name: "",
          sex: "",
          sfzh: "",
          ticket: "",
          tele: "",
          email: "",
          zycc: "",
          zymc: "",
          kcdm: "",
          file: ""
      },
    }
  },
  created() {
    // 
    this.$public.tokenlogin2(this.$cookies,this.axios,this.$store,this.$router,this.$message)
  },
  mounted:function(){
    //
    this.JudgeRes();//
  },
  methods:{
    //
    regoSignUp(){
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //loading
        const load = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        let params = new URLSearchParams();
        params.append("token",this.$cookies.get("token"))
        this.axios.post("deletesignup",
            params
        ).then((response) => {
          load.close()
          //console.log(response.data)
          //console.log(response.data['msg'])

          if (response.data['msg'] == "ok") {
              this.goSignUp()     //
              this.$forceUpdate()         //
          }

        })
        this.$message({
          type: 'success',
          message: '!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });

    },
    //  file
    segmenteFile(file){
      if( file == null )  return null       //
      let array = file.split('|');
      //console.log(array)
      //console.log(array.length)
      let list = [];
      for(let i = 0 ; i < array.length ; i++ ){
        let names = array[i].split('_');
        list.push({'fname':names[1],'fsrc': this.$store.state.baseurl +array[i] + '?token=' +this.$cookies.get("token")  })
      }
      //console.log(list)
      return list;
    },
    //
    JudgeRes(){
      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      let params = new URLSearchParams();
      params.append("token",this.$cookies.get("token"))
      this.axios.post("signupres",
          params
      ).then((response) => {
        load.close()
        //console.log(response.data)
        //console.log(response.data['msg'])

        if( response.data['msg'] == "ok"){
          //  res01-12
          if( response.data['res'] == "2" ){      //
            this.steps = "1";
          }else if( response.data['res'] == "0"  ){     //
            this.steps = "2";
            //loading
            const load = this.$loading({
              lock: true,
              text: '',
              spinner: 'el-icon-loading',
              background: 'rgba(0, 0, 0, 0.7)'
            });
            let param = new URLSearchParams();
            param.append("token",this.$cookies.get("token"))
            this.axios.post("signupinfo",
                param
            ).then((response) => {
              load.close()
              console.log(this.infoData)
              this.infoData.name = response.data['name']
              this.infoData.sex = response.data['sex']
              this.infoData.ticket = response.data['ticket']
              this.infoData.sfzh = response.data['sfzh']
              this.infoData.tele = response.data['tele']
              this.infoData.email = response.data['email']
              this.infoData.zycc = response.data['zycc']
              this.infoData.zymc = response.data['zymc']
              this.infoData.kcdm = response.data['kcdm']
              this.infoData.file = response.data['file']
            })
          }else if( response.data['res'] == "1" || response.data['res'] == "-1"  ){     //
            this.steps = "3";
            this.result = response.data['res'];
          }

        }
        this.$forceUpdate()         //
      })
    },
    //
    diaCancel(){

      this.dialogFormVisible = false;
      //this.JudgeRes();
      //location.reload();
      this.$forceUpdate()

    },
    //
    goSignUp(){
      this.$router.push("/success/signupinfo");
    },
    //
    AddOption(){
      if( this.form.zycc == "" ){
        this.options_zymc = [
          {
            value: '',
            label: '',
          },
          {
            value: '',
            label: '',
          },
        ]
      }else if( this.form.zycc == "" ) {
        this.options_zymc = [
          {
            value: '',
            label: '',
          },
          { value: '',
            label: '',
          },
        ]
      }
    },
    Change(){
      //this.GetCode();
      this.form.zymc = ""
      if( this.form.zycc == "" ){
        this.options_zymc = [
          {
            value: '',
            label: '',
          },
          {
            value: '',
            label: '',
          },
        ]
      }else if( this.form.zycc == "" ) {
        this.options_zymc = [
          {
            value: '',
            label: '',
          },
          { value: '',
            label: '',
          },
        ]
      }
    },
    //
    modifySign(){

      this.fileList = []

      this.getCode();             //
      this.dialogFormVisible = true;
      //!!!
      //this.form  = this.infoData

      this.form.name = this.infoData.name
      this.form.sex = this.infoData.sex
      this.form.ticket = this.infoData.ticket
      this.form.sfzh = this.infoData.sfzh
      this.form.tele = this.infoData.tele
      this.form.email = this.infoData.email
      this.form.zycc = this.infoData.zycc

      this.AddOption()
      this.$forceUpdate()         //

      this.form.zymc = this.infoData.zymc
      this.form.kcdm = this.infoData.kcdm
      this.form.file = this.infoData.file

      //
      //1.
      if( this.infoData.sex == "" ){
        this.form.sex = "1"
      }else{
        this.form.sex = "2"
      }
      //2.
      this.form.kcdm = []
      //this.form.kcdm = this.infoData.kcdm
      // 
      let array = this.infoData.kcdm.toString().split(",")
      for(let i = 0 ; i < array.length ; i++ ){
        //his.$set(this.form.kcdm, array[i], [])
        this.form.kcdm.push(array[i])
      }
      console.log(this.form.kcdm)
      console.log(this.infoData.kcdm)
      this.$forceUpdate()         //
    },
    //
    SubmitModify(){
      let error = 0;
      this.$refs.modifysignup.validate((valid) => {
        if (!valid) {
          this.$message({
            type: 'error',
            message: ''
          });
          error = 1;
          return;
        } else {
          //
        }
      });
      if( error == 1 )  return;

      /*if( this.fileList.length == 0 ){
        this.$message({
          type: 'error',
          message: ''
        });
        return
      }*/


      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //loading
        const load = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        this.$refs.upload.submit();
        //console.log(this.param)

        this.param.append("token",this.$cookies.get("token"))
        this.param.append('name',this.form.name);
        this.param.append('sex',this.form.sex);

        this.param.append('sfzh',this.form.sfzh);
        this.param.append('ticket',this.form.ticket);
        this.param.append('tele',this.form.tele);
        this.param.append('email',this.form.email);
        this.param.append('zycc',this.form.zycc);
        this.param.append('zymc',this.form.zymc);
        this.param.append('kcdm',this.form.kcdm);
        //    
        let configs = {
          headers: { "Content-Type": "multipart/form-data" }
        };
        //console.log(configs)
        this.axios.post("modifyupload",
            this.param,configs
        ).then((response) => {
          load.close()
          //console.log(response.data)
          if (response.data['msg'] === "ok") {

            this.dialogFormVisible = false;
            this.JudgeRes()
            this.$forceUpdate()         //
          }
        })
        this.$message({
          type: 'success',
          message: '!'
        });
        this.fileList = []
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    //
    getCode(){
      //console.log("" )
      let params = new URLSearchParams()
      params.append("token",this.$cookies.get("token"))
      this.axios.post("showcode",params
      ).then((response) => {
        //console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.kcdm = response.data["course"]
        }
      })
    },

    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    beforeUpload(file) {
      this.param = new FormData();
      this.fileList.push(file);
      let images = [...this.fileList];
      //console.log("images", images);

      //this.param.append("file1", images);

      images.forEach((img) => {
        this.param.append("file", img); // 
      });
      return false;
    },


  }
}
</script>

<style scoped>

</style>