<template>
  <div style="height: 100vh;" >
    <el-container class="Contain">
      <el-header>
        <Header>
        </Header>
          <div style=" right: 76px;top: 55px; position: absolute;z-index: 3;">
            <el-dropdown>
              <div>
                <el-avatar icon="el-icon-user">
                  <i class="el-icon-arrow-down"></i>
                </el-avatar>
                <div style="color: #FFFFFF;text-align: center" > {{ help.name }}</div>
              </div>

              <el-dropdown-menu>
                <!-- el-dropdown-item.native -->
                <el-dropdown-item @click.native="goInfo"></el-dropdown-item>
                <el-dropdown-item @click.native="goModify"></el-dropdown-item>
                <el-dropdown-item @click.native="logout"></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>

      </el-header>
      <el-container>
        <el-side width="200px" style="min-width:200px;max-width:200px;margin-top: 90px;">
          <el-menu
              default-openeds="['1']"
              background-color="#fff"
              text-color="#000000"
              active-text-color="#409EFF"
              :default-active="$route.path"
              @open="handleOpen"
              @close="handleClose"
              router>
            <el-menu-item index="/success/successindex" >
              <i class="el-icon-house"></i>
              </el-menu-item>
            <el-submenu index="1">
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span slot="title"></span>
              </template>
              <el-menu-item index="/success/signupinfo">
                <i class="el-icon-edit"></i>
                </el-menu-item>
              <el-menu-item index="/success/registerstatus">
                <i class="el-icon-notebook-2"></i>
                </el-menu-item>
            </el-submenu>
            <!--
            <el-submenu v-for="(item, i) in nav" :key="i" :index="item.index">
              <template slot="title">
                <i class="el-icon-place"></i>
                <span>{{ item.title }}</span>
              </template>
              <el-menu-item v-for="(option, j) in item.options" :key="j" :index="`${item.index}/${j+1}`">{{option}}</el-menu-item>
            </el-submenu>
            -->
            </el-menu>
        </el-side>
        <el-main style="margin-top: 90px;margin-bottom: 30px">
          <!--  -->
          <router-view></router-view>
        </el-main>
      </el-container>
      <!--
      <el-foot>
        <Footer></Footer>
      </el-foot>
      -->
    </el-container>
  </div>
</template>

<script>
import Header from "@/components/Header";
//import Footer from "@/components/Footer";
import ElementUI from "element-ui";

export default {
  name: "Success",
  components: {Header},
  data() {
    return {
      // 
      help: { name:"",other:""},      // 
      nav: [
        {
          title: '',
          index: "/success/signupinfo",
          options: [
            '',
            '',
          ]
        },
        {
          title: '',
          index: 2,
          options: [
            '',
            '',
          ]
        },
      ],
      Height:0,
    }
  },
  created() {
    // 
    this.$public.tokenlogin(this.$cookies,this.axios,this.$store,this.$router,this.$message,this.help)
  },
  methods:{
    //
    goInfo(){
      //console.log("asd")
      this.$router.push("/success/personinfo");
    },
    //
    goModify(){
      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      //
      let params = new URLSearchParams();
      params.append("token",this.$cookies.get("token"))
      params.append('username', this.$store.state.user.username);
      this.axios({
        method: 'post',
        url:"forgetpass",
        withCredentials:true,
        data:params,
        dataType:'JSONP',  // Ajax,
      }).then((response) => {
        load.close()
        //console.log(response.data)
        //console.log(response.data['msg'])
        if( response.data['msg'] == "ok"){
          //console.log("help")
          // user
          this.$store.commit('forget',this.$store.state.user.username)
          ElementUI.Message({
            message: '',
            type: 'success'
          });
          this.$router.push("/forgetpass?which=2")
        }else{          //
          //this.error_msg = response.data['error']
          ElementUI.Message({
            message: '',
            type: 'error'
          });
        }
      })

    },
    //
    logout(){
      this.$store.commit('logout')
      this.$cookies.remove('token')
      //
      this.$router.push("/index");
    },
    /*
    getParams(){
      // 
      this.$forceUpdate()
      //console.log(this.$store.state.user.name)
      this.name = this.$store.state.user.name;
    },*/
    // ,
    toggleCollpase() {
      this.isCollapse = !this.isCollapse
    },
    saveActivePath(activePath) {
      window.sessionStorage.setItem('activePath', activePath)
      this.activePath = activePath
      //console.log(this.$route.path)
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
  }
}
</script>

<style lang="less" scoped>
@import "../public.css";
.el-container{
  /*0*/
  padding: 0px;
  /**/
  margin: 0px;
  height:100%;
  width:100%;

}
.Contain{
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;     //column  
  justify-content:space-between;    //
}
.el-footer {
  display: flex;
  height: 4vh;
  width: 100%;
  align-items: center;
}
</style>