<template>
  <div style="height: 100vh">
    <el-container>
      <el-header>
        <Header></Header>
      </el-header>
      <el-header>
        <!--  -->
        <!--
        <div class="main-navigation">
          <div class="main-nav">
            <div class="row">
              <div class="collapse navbar-collapse" id="main-menu">
                <ul class="menu nav navbar-center">
                  <li><a href="index"></a></li>
                </ul> </div> </div> </div></div>
                -->
      </el-header>
      <el-container>
        <el-side style="min-width:60%;">
          <div class="content">
            <div class="TopPic"></div>
            <div class="contentWrap">
              <div class="colum_1">
                <h1></h1>
                <p class="introTxt">
                  <br /><br />
                  <span style="color:#da4837;font-weight:bold"> </span>
                      <br />
                  
                  <br /> <span style="color:#da4837;font-weight:bold"> </span>
                </p>
                <br><br><br><br><br><br>
              </div> </div>

          </div>
        </el-side>
        <el-main>
          <!--
          prop model  validateresetFields 
          -->
          <!-- direction="vertical"-->
          <div class="table">
            <div class="sontable">
              <h2 style="margin-top: 10px"></h2>
              <span class="STYLE2" style="font-size:14px;color:#CC0000;" >&nbsp;{{error_msg}}</span>
              <el-form :model="param" :rules="rules" ref="adminlogin" label-width="80px">
                <el-form-item label="" prop="username">
                  <el-input type="text" placeholder="" size="small"
                            prefix-icon="el-icon-user"
                            v-model="param.username"></el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                  <el-input type="password" placeholder="" size="small" show-password prefix-icon="el-icon-lock"
                            v-model="param.password"></el-input>
                </el-form-item>

                <el-form-item label="" prop="yzm">
                  <el-col :span="12" >
                    <el-input type="text" placeholder="" prefix-icon="el-icon-picture" style="width: 100px"
                    v-model="param.yzm"></el-input>
                  </el-col>
                  <el-col :span="12">
                    <Canvas></Canvas>
                  </el-col>
                </el-form-item>

                <div style="margin-left: 26px">
                  <el-col :span="12" >
                    <el-button type="primary" @click="Submit"></el-button>
                  </el-col>
                  <el-col :span="12">
                    <el-button type="primary" @click="goHome"></el-button>
                    <!--
                    <el-space wrap size="9px">
                    </el-space>
                    -->
                  </el-col>
                </div>
              </el-form>
            </div>
          </div>
        </el-main>
      </el-container>
      <el-foot>
        <div style="text-align: center">
          <el-link type="default" style="text-align: center" @click="goHome"></el-link>
        </div>
        <Footer></Footer>
      </el-foot>
    </el-container>
  </div>
</template>

<script>
import Canvas from "@/components/Canvas";
import Header from "@/components/Header";
import Footer from "@/components/Footer";

export default {
  //
  name: "AdminLogin",
  components: {Canvas, Header, Footer},
  data(){
    return {
      param:{
        username:"",
        password:"",
        yzm:"",
        error_msg:"",
      },
      //
      //
      rules:{
        username:[
          { required:true,message:"",trigger:["blur"]},
          { pattern: /^[\u4e00-\u9fa5a-zA-Z0-9]+$/,message: '',trigger:["blur","change"] },
        ],
        password:[
          {required:true,message:"",trigger:["blur"]},
          { min: 6, message: '6', trigger: ["blur","change"] },
          { pattern: new RegExp(/^\S*$/, 'g'), message: '',trigger:["blur","change"] }
        ],
        yzm:[
          {required:true,message:"",trigger:["blur"]},
          { pattern: /^[\u4e00-\u9fa5a-zA-Z0-9]+$/,message: '',trigger:["blur","change"] },
          { min: 4, max: 4, message: '4', trigger: ["blur","change"] }
        ],
      }
    }

  },
  methods: {
    Submit() {
      // 
      let error = 0;
      this.$refs.adminlogin.validate((valid) => {
        if (!valid) {
          //console.log("As")
          this.$message({
            type: 'error',
            message: ''
          });
          error = 1;
          return;
        } else {
          //
        }
      });
      if( error == 1 )  return;

      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });

      //axiosnull
      let params = new URLSearchParams();
      params.append('username', this.param.username);
      params.append('password', this.param.password);
      params.append('yzm', this.param.yzm);

      this.axios({
        method: 'post',
        url:"adminlogin",
        withCredentials:true,
        data:params,
        dataType:'JSONP',  // Ajax,
      }).then((response) => {
        load.close()
        if( response.data['msg'] == "ok"){        //
          //token  cookiekey=oatoken1
          this.$cookies.set("token",response.data['token'],'1d')
          // user
          this.$store.commit('adminlogin', response.data)
          this.$router.push("/adminsuccess/adminindex");        //
        }else{          //
          this.$forceUpdate()     //
          this.error_msg = response.data['error']
        }
      })
    },
    ///
    goHome() {
      this.$router.push("/index");
    },
  }
}
</script>

