<template>
  <div style="height: 100vh">
    <el-container>
      <el-header>
        <Header></Header>
      </el-header>
      <el-container>
        <el-main>
          <!--
          prop model  validateresetFields 
          -->
          <!-- direction="vertical"-->

          <el-col :span="12">
            <div class="table">
              <div class="sontable">

                <h1 v-if="which==1">/</h1>
                <h1 v-if="which==2"></h1>
                <el-divider></el-divider>

                <div>
                  <el-form :model="param" :rules="rules" ref="forgetpass" label-width="80px">

                    <el-form-item label="" prop="eyzm">
                      <el-input type="text" placeholder="" size="small"
                                prefix-icon="el-icon-picture"
                                v-model="param.eyzm"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="password">
                      <el-input type="password" placeholder="6" size="small"
                                show-password prefix-icon="el-icon-lock"
                                v-model="param.password"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="qrpassword">
                      <el-input type="password" placeholder="" size="small"
                                show-password prefix-icon="el-icon-lock"
                                v-model="param.qrpassword"></el-input>
                    </el-form-item>
                    &nbsp;&nbsp;&nbsp;
                    <el-button type="primary" @click="Submit"></el-button>
                    &nbsp;
                    <el-button type="primary" @click="goBack"></el-button>
                  </el-form>
                </div>


              </div>
            </div>
          </el-col>
          <el-col :span="12">
            <div style="margin-left: 10px;margin-top: 100px">
              <el-card width="300px">
                <span></span>
                <p>
                  <br/>
                  <br/>5. </p>
              </el-card>
            </div>
          </el-col>
        </el-main>
      </el-container>
      <el-foot>
        <Footer></Footer>
      </el-foot>
    </el-container>
  </div>
</template>

<script>

import Header from "@/components/Header";
import Footer from "@/components/Footer";
import ElementUI from "element-ui";

export default {
  name: "Forgetpass",
  components: { Header, Footer},
  data(){
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
        // password 
      } else if (value !== this.param.password) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    return {
      // 
      which:"1",
      type:"",
      username:"",
      // 
      param:{
        eyzm:"",
        password:"",
        qrpassword:"",
      },
      //
      rules:{
        eyzm:[
          {required:true,message:"",trigger:"blur"}
        ],
        password:[
          {required:true,message:"",trigger:"blur"},
          {min:6,max:30,message:"630",trigger:"blur"}
        ],
        qrpassword:[
          { required: true, validator: validatePass2, trigger: 'blur' }
        ],
      }
    }
  },
  mounted() {
    // 
    //get
    let which  = this.$route.query.which
    if( which == "2" ){
      this.which = "2";
    }else{
      this.which = "1";
    }
  },
  methods:{
    // 
    goBack(){
      this.$router.go(-1);
    },
    Submit(){
      let error = 0;
      this.$refs.forgetpass.validate((valid) => {
        if (!valid) {
          //console.log("As")
          this.$message({
            type: 'error',
            message: ''
          });
          error = 1;
          return;
        } else {
          //
        }
      });
      if( error == 1 )  return;

      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      //console.log(this.$store.state.username)
      //axiosnull
      let params = new URLSearchParams();
      params.append('username',this.$store.state.username);
      params.append('password', this.param.password);
      params.append('eyzm', this.param.eyzm);

      this.axios({
        method: 'post',
        url:"modifypass",
        withCredentials:true,
        data:params,
        dataType:'JSONP',  // Ajax,
      }).then((response) => {
        /*
        * {'msg':'ok'}
        * {'msg':'fail','error':''}
        */
        //console.log(response.data)
        //console.log(response.data['msg'])
        load.close()
        if( response.data['msg'] == "ok"){        //
          // user
          this.$store.commit('login', response.data)
          if( this.which === "1" ){
            ElementUI.Message({
              message: '5',
              type: 'success'
            });
            setTimeout(
                //5
                this.$router.push("/login")         //
                , 5000);
          }else{
            ElementUI.Message({
              message: '5',
              type: 'success'
            });
            setTimeout(
                //5
                this.$router.go(-1)         //
                , 5000);
          }
        }else{      //
          ElementUI.Message({
            message: ''+response.data['error']+"",
            type: 'error'
          });
        }

      })
    }
  }
}
</script>

<style lang="less" scoped>

/**/
.table{
  margin-top:20px;
  margin-left: 20px;
  width: 600px;
  font-size:14px;
  line-height:34px;
}
.sontable{
  margin-top:100px;
  margin-left:300px;
}
.el-card{
  width: 270px;
  height: 320px;
  margin-top:100px;
  margin-left:150px;
  span{
    font-weight: bolder;
    font-size:17px;
  }
  p{
    margin-top:20px;
    font-size:14px;
    color:#424242;
    line-height:34px;
  }
}
.el-input{
  width:240px;
}
</style>