<template>
  <div style="height: 100vh">
    <template>
      <el-descriptions title=""
                       :column="2"
                       size="large"
                       style="width: 50%;margin-left: 15%"
                       border>
        <el-descriptions-item label="">{{infoData.username}}</el-descriptions-item>
        <el-descriptions-item label="">{{infoData.name}}</el-descriptions-item>
        <el-descriptions-item label="">{{infoData.sex}}</el-descriptions-item>
        <el-descriptions-item label="">{{infoData.sfzh}}</el-descriptions-item>
        <el-descriptions-item label="">{{infoData.tele}}</el-descriptions-item>
        <el-descriptions-item label="">{{infoData.email}}</el-descriptions-item>
      </el-descriptions>
      <br />
      <el-button type="primary" style="margin-left: 20%" @click="editInfo">/</el-button>
    </template>

    <!--  -->
    <el-dialog :visible="dialogFormVisible" title="/" :close-on-click-modal="true"
               @close="dialogFormVisible=false"
                :append-to-body="true">
      <el-form :model="form"  :rules="rules" ref="modify" label-width="80px">
        <el-form-item label="" prop="username" >
          <el-input type="text" placeholder="" size="small"
                    prefix-icon="el-icon-user"
                    v-model="form.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input type="text" placeholder="" size="small"
                    prefix-icon="el-icon-s-ticket"
                    v-model="form.name"></el-input>
        </el-form-item>


        <el-form-item label="" prop="sex">
          <div>
            <el-radio v-model="form.sex" label="1" size="large"></el-radio>
            <el-radio v-model="form.sex" label="2" size="large"></el-radio>
          </div>
        </el-form-item>

        <el-form-item label="" prop="email">
          <el-input type="text" placeholder="" size="small"
                    prefix-icon="el-icon-message"
                    v-model="form.email"></el-input>
        </el-form-item>

        <el-form-item label="" prop="tele">
          <el-input type="text" placeholder="" size="small"
                    prefix-icon="el-icon-phone"
                    v-model="form.tele"></el-input>
        </el-form-item>

        <el-form-item label="" prop="sfzh">
          <el-input type="text" placeholder="" size="small"
                    prefix-icon="el-icon-postcard"
                    v-model="form.sfzh"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
          <span class="dialog-footer">
            <el-button @click="dialogFormVisible = false"></el-button>
            <el-button type="primary" @click="modifyInfo"></el-button>
          </span>
      </template>
    </el-dialog>
  </div>
</template>
<script>
//
export default {
  name: "PersonInfo",

  data(){
    var validateIdNo = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
        return
      }
      var format = /^(([1][1-5])|([2][1-3])|([3][1-7])|([4][1-6])|([5][0-4])|([6][1-5])|([7][1])|([8][1-2]))\d{4}(([1][9]\d{2})|([2]\d{3}))(([0][1-9])|([1][0-2]))(([0][1-9])|([1-2][0-9])|([3][0-1]))\d{3}[0-9xX]$/;
      //
      if (!format.test(value)) {
        callback(new Error(''));
      }
      //
      //  1900;
      var year = value.substr(6, 4),//
          month = value.substr(10, 2),//
          date = value.substr(12, 2),//
          time = Date.parse(month + '-' + date + '-' + year),//date
          now_time = Date.parse(new Date()),//
          dates = (new Date(year, month, 0)).getDate();//
      if (time > now_time || date > dates) {
        callback(new Error(''));
      }
      //
      var c = new Array(7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2);  //
      var b = new Array('1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2'); //
      var id_array = value.split("");
      var sum = 0;
      for (var k = 0; k < 17; k++) {
        sum += parseInt(id_array[k]) * parseInt(c[k]);
      }
      if (id_array[17].toUpperCase() != b[sum % 11].toUpperCase()) {
        callback(new Error(''));
      }
      callback();
    }
    return {
      type:"",
      //
      infoData:{
        username:"",
        name:"",
        sex:"",
        sfzh:"",
        tele:"",
        email:"",
      },
      //
      dialogFormVisible:false,
      // 
      form:{},
      //
      rules:{
        name:[
          {required:true,message:"",trigger:"blur"},
          { pattern: /^[\u4E00-\u9FA5]+$/i,message: '',trigger:["blur","change"] },
        ],
        sex:[
          {required:true,message:"",trigger:"blur"}
        ],
        sfzh:[
          {required:true,validator: validateIdNo,trigger:"blur"}
        ],
        tele:[
          { pattern: new RegExp(/^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/,'g'),
            message: '',trigger:"change"}
        ],
        email:[
          {type:"email",message:"",trigger:"blur"},
          {required:true,message:"",trigger:["blur","change"] }
        ],
      }
    }
  },
  created() {
    // 
    this.$public.tokenlogin2(this.$cookies,this.axios,this.$store,this.$router,this.$message)
  },
  mounted() {
    this.getInfo()
  },
  methods:{
    //
    getInfo(){
      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });

      let params = new URLSearchParams()
      params.append("token",this.$cookies.get("token"))
      this.axios.post("personinfo",
          params
      ).then((response) => {
        load.close()
        //console.log(response.data)
        //console.log(response.data['msg'])

        if( response.data['msg'] == "ok"){
          this.infoData.name = response.data['name']
          this.infoData.sex = response.data['sex']
          if( response.data['sex'] == "1" ){
            this.infoData.sex = ""
          }else{
            this.infoData.sex = ""
          }
          this.infoData.username = response.data['ticket']
          this.infoData.tele = response.data['tele']
          this.infoData.email = response.data['email']
          this.infoData.sfzh = response.data['sfzh']
          this.$forceUpdate()         //
        }
      })
    },
    //
    editInfo(){

      this.dialogFormVisible = true;
      if( this.infoData.sex == "" ){
        this.infoData.sex = "1"
      }else{
        this.infoData.sex = "2"
      }
      this.form = this.infoData
      this.$forceUpdate()         //
    },
    //
    modifyInfo(){
      this.dialogFormVisible = false;
      if( this.infoData.username == this.form.username
          &&
          this.infoData.name == this.form.name
          &&
          this.infoData.sfzh == this.form.sfzh
          &&
          this.infoData.tele == this.form.tele
          &&
          this.infoData.email == this.form.email
          &&
          (( this.infoData.sex == "" && this.form.sex == "1") || ( this.infoData.sex == "" && this.form.sex == "2"))
      ){
        //
        this.$message({
          type: 'error',
          message: ''
        });
        return;
      }
      //console.log(index+" "+this.codeData[index]['code'])
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //loading
        const load = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });

        let params = new URLSearchParams()
        params.append("token",this.$cookies.get("token"))
        params.append('username',this.form.username);
        params.append('sex',this.form.sex);
        params.append('name',this.form.name);
        params.append('tele',this.form.tele);
        params.append('email',this.form.email);
        params.append('sfzh',this.form.sfzh);

        this.axios.post("modifyinfo",
            params
        ).then((response) => {
          load.close()
          //console.log(response.data)
          if (response.data['msg'] == "ok") {

            this.getInfo()
            this.$forceUpdate()         //
          }
        })
        this.$message({
          type: 'success',
          message: '!'
        });
        this.getInfo()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
  }
}
</script>

<style scoped>
.my-label {
  background-color:#3b6496;
  color:#3b6496;
}
</style>