import Vue from 'vue'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

import axios from 'axios'
import VueAxios from 'vue-axios'
import Vuex from 'vuex'
import store from './Vuex'
import common from './common'   // common.js
import 'lib-flexible'           // vuesrcmain.js
import "./public.css"           // css
import "./mobile.css"           // css
//import 'amfe-flexible'
//cookieshttps://blog.csdn.net/weixin_46371752/article/details/122321152
import cookies from 'vue-cookies'
import * as fun from './public';
//https://blog.csdn.net/qq_45616003/article/details/118939568
import FileSaver from "file-saver";
import * as XLSX from "xlsx";
import App from './App.vue'
import router from "./router"



Vue.prototype.$common = common;    // 
axios.defaults.baseURL = common.baseURL;


Vue.prototype.$cookies = cookies;

Vue.prototype.$public = fun;        //js:https://www.jianshu.com/p/00bc0a63a50f



Vue.prototype.$fileSaver = FileSaver
Vue.prototype.$xlsx = XLSX


Vue.use(Vuex)
Vue.use(VueAxios, axios)
Vue.use(ElementUI)//ElementUI
Vue.config.productionTip = false


new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
