<template>
  <div style="height: 100vh">
    <el-container >
        <el-header>
          <Header></Header>
        </el-header>
      <el-header>
        </el-header>
      <el-container  v-show="show==1" >
      <el-side style="min-width:60%;">
        <div class="content">
          <div class="TopPic"></div>
          <div class="contentWrap">
            <div class="colum_1">
              <h1></h1>
              <p class="introTxt">
                <br /><br />
                <span style="color:#da4837;font-weight:bold"></span>
                <br/></p>
              <br><br><br><br><br><br>
              <div class="regSection">
              
              <el-button type="danger" @click="goRegister">
                
              </el-button>
              </div>
            </div> </div>

        </div>
      </el-side>
      <el-main>

      <!--
      prop model  validateresetFields 
      -->
        <!-- direction="vertical"-->
        <div class="table">
          <div class="sontable">
            <h2 style="margin-top: 10px"></h2>
            <span class="STYLE2" style="font-size:14px;color:#CC0000;" >&nbsp;{{error_msg}}</span>

            <el-form :model="param" :rules="rules" ref="login" label-width="80px">
              <el-form-item label="" prop="username">
                <el-input type="text" placeholder="" size="small"
                          prefix-icon="el-icon-user"
                          v-model="param.username"></el-input>
              </el-form-item>
              <el-form-item label="" prop="password">
                <el-input type="password" placeholder="" size="small" show-password prefix-icon="el-icon-lock"
                          v-model="param.password"></el-input>
              </el-form-item>


              <el-form-item label="" prop="yzm">
                <el-col :span="12" >
              <el-input type="text" placeholder="" prefix-icon="el-icon-picture" style="width: 100px"
                        v-model="param.yzm"></el-input>

                </el-col>
                <el-col :span="12">
                  <Canvas></Canvas>
                </el-col>
              </el-form-item>


              <div style="margin-left: 26px">
              <el-col :span="12" >
                <el-button type="primary" @click.enter="SubmitForm"></el-button>
              </el-col>
              <el-col :span="12">
                <el-button type="primary" @click="goForget">/</el-button>
                <!--
                <el-space wrap size="9px">
                </el-space>
                -->
              </el-col>
              </div>
              </el-form>
          </div>
        </div>
      </el-main>
        </el-container>
      <el-container  v-show="show==0" >
          <div class="content">
            <div class="TopPic"></div>
            <div class="contentWrap">
              <div class="colum_1" style="margin-left: 15%">
                <h1></h1>
                <p class="introTxt">
                </p>
                <el-result
                    icon="warning"
                    title=""
                    sub-title=""
                ></el-result>

              </div> </div>
          </div>
      </el-container>

      <el-foot>
        <div style="text-align: center">
        <el-link type="default" style="text-align: center" @click="goAdminLogin"></el-link>
        </div>
        <Footer></Footer>
      </el-foot>
    </el-container>
  </div>
</template>


<script>
//https://blog.csdn.net/weixin_39237340/article/details/121666311
//https://blog.csdn.net/qq_33820545/article/details/108529789
//https://blog.csdn.net/weixin_39237340/category_11504745.html
import Footer from "@/components/Footer";
import Header from "@/components/Header";
import Canvas from "@/components/Canvas";
import ElementUI from "element-ui";


export default {
  name: "login",
  components: {Canvas, Header, Footer},
  data(){
    return {
      show:"0",
      param:{
        username:"",
        password:"",
        yzm:"",
        error_msg:""
      },
      //
      rules:{
        username:[
          { required:true,message:"",trigger:["blur"]},
          { pattern: /^[\u4e00-\u9fa5a-zA-Z0-9]+$/,message: '',trigger:["blur","change"] },
        ],
        password:[
          {required:true,message:"",trigger:["blur"]},
          { min: 6, message: '6', trigger: ["blur","change"] },
          { pattern: new RegExp(/^\S*$/, 'g'), message: '',trigger:["blur","change"] }
        ],
        yzm:[
          {required:true,message:"",trigger:["blur"]},
          { pattern: /^[\u4e00-\u9fa5a-zA-Z0-9]+$/,message: '',trigger:["blur","change"] },
          { min: 4, max: 4, message: '4', trigger: ["blur","change"] }
        ],
      }
    }

  },
  mounted:function(){
    this.JudgeTime();//
  },
  methods:{
    JudgeTime(){
      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });

      this.axios.post("opentime"
      ).then((response) => {

        //console.log(response.data)
        //console.log(response.data['msg'])

        if( response.data['msg'] == "ok"){        //
          this.show = "1"
        }else{          //
          this.show = "0"
        }
        this.$forceUpdate()         //
        load.close()
      })
    },
    // 
    SubmitForm(){
      // 
      let error = 0;
      this.$refs.login.validate((valid) => {
            if (!valid) {
              //console.log("As")
              this.$message({
                type: 'error',
                message: ''
              });
              error = 1;
              return;
            } else {
              //
            }
      });
      if( error == 1 )  return;


      //loading
      const load = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      //axiosnull
      let params = new URLSearchParams();
      params.append('username', this.param.username);
      params.append('password', this.param.password);
      params.append('yzm', this.param.yzm);


      this.axios({
        method: 'post',
        url:"login",
        withCredentials:true,
        data:params,
        dataType:'JSONP',  // Ajax,
      }).then((response) => {
        //console.log(response.data)
        /*
        emailmsgnameticketusername
        * */
        //console.log(response.data['msg'])
        load.close()
        if( response.data['msg'] == "ok"){        //
          //:https://www.cnblogs.com/hthuang/p/7646472.html
          // user
          this.$store.commit('login', response.data)
          ElementUI.Message({
            message: '',
            type: 'success'
          });
          //token  sessionStorage 
          //sessionStorage.setItem("token",response.data['token']);
          //token  cookies 
          this.$cookies.set("token",response.data['token'],'1d')
          this.$router.push(
              {
                path:"/success/successindex",
              }
          );        //
        }else{      //

          this.error_msg = response.data['error']
          this.Canvas.methods.createCode();         //,
          this.$forceUpdate()         //
          //this.$forceUpdate()
        }

      })
    },
    ///
    goHome(){
      this.$router.push("/index");
    },
    //
    goRegister(){
      this.$router.push("/register");
    },
    //
    goAdminLogin(){
      this.$router.push("/adminlogin");
    },
    //
    goForget(){
      this.$router.push("/forget");
    },
  }
}

</script>

