<template>
  <el-container direction="vertical" style="height: 1000px">
    <el-main >
      <!--
      prop model  validateresetFields 
      -->
      <!-- direction="vertical"-->

      <el-col :span="9">
        <div class="table">
          <div class="sontable">
            <h1></h1>
            <el-divider></el-divider>
            <el-form :model="form" :rules="rules" ref="signup" label-width="80px" :disabled="this.steps!== '1'">
              <el-form-item label="" prop="name">
                <el-input type="text" placeholder="" size="small"
                          prefix-icon="el-icon-s-ticket"
                          v-model="form.name"></el-input>
              </el-form-item>
              <el-form-item label="" prop="sex">
                <div>
                  <el-radio v-model="form.sex" label="1" size="large"></el-radio>
                  <el-radio v-model="form.sex" label="2" size="large"></el-radio>
                </div>
              </el-form-item>
              <el-form-item label="" prop="sfzh">
                <el-input type="text" placeholder="" size="small"
                          prefix-icon="el-icon-postcard"
                          v-model="form.sfzh"></el-input>
              </el-form-item>

              <el-form-item label="" prop="ticket">
                <el-input type="text" placeholder="" size="small"
                          prefix-icon="el-icon-tickets"
                          v-model="form.ticket" disabled></el-input>
              </el-form-item>
              <el-form-item label="" prop="tele">
                <el-input type="text" placeholder="" size="small"
                          prefix-icon="el-icon-phone"
                          v-model="form.tele"></el-input>
              </el-form-item>
              <el-form-item label="" prop="email">
                <el-input type="text" placeholder="" size="small"
                          prefix-icon="el-icon-message"
                          v-model="form.email"></el-input>
              </el-form-item>
              <el-form-item label="" prop="zycc">
                <el-select v-model="form.zycc" placeholder="Select" size="small" @change="Change">
                  <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="" prop="zymc">
                <el-select v-model="form.zymc" placeholder="Select" size="small">
                  <el-option
                      v-for="item in options_zymc"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="" prop="kcdm">
                <el-checkbox-group v-model="form.kcdm">
                  <el-checkbox  v-for="item in kcdm"
                                :label="item.name+'('+item.code+')'"
                                :key="item.code"
                  >
                  </el-checkbox>
                </el-checkbox-group>
              </el-form-item >
              <el-form-item label="" prop="file">
              <el-upload
                  ref="upload"
                  list-type="text"
                  :on-preview="handlePictureCardPreview"
                  :before-upload="beforeUpload"
                  :on-remove="handleRemove"
                  :auto-upload="false"
                  :multiple="true"
                  :limit="3"
                  show-file-list
              >
                <el-button type="primary" ></el-button>
                <!--<el-button type="primary"></el-button>-->
                <template #tip>
                  <div class="el-upload__tip">
                    JPG/PNG/PDF500KB
                  </div>
                </template>
              </el-upload>
              </el-form-item>
              &nbsp;&nbsp;&nbsp;&nbsp;<el-button type="danger" @click="Submit"></el-button>
              &nbsp;&nbsp;&nbsp;<el-button type="primary" @click="goIndex"></el-button>

            </el-form>
          </div>
        </div>
      </el-col>
      <el-col :span="9">
        <div style="margin-left: 10px;margin-top: 100px">
          <el-card style="width: 250px;height: 380px">
            <span></span>
            <p>
            <br>
            <br>
            <br>
            
            </p>
          </el-card>
        </div>
      </el-col>
    </el-main>
  </el-container>

</template>

<script>

export default {
  name: "Signupinfo",
  data(){
    var validateIdNo = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
        return
      }
      var format = /^(([1][1-5])|([2][1-3])|([3][1-7])|([4][1-6])|([5][0-4])|([6][1-5])|([7][1])|([8][1-2]))\d{4}(([1][9]\d{2})|([2]\d{3}))(([0][1-9])|([1][0-2]))(([0][1-9])|([1-2][0-9])|([3][0-1]))\d{3}[0-9xX]$/;
      //
      if (!format.test(value)) {
        callback(new Error(''));
      }
      //
      //  1900;
      var year = value.substr(6, 4),//
          month = value.substr(10, 2),//
          date = value.substr(12, 2),//
          time = Date.parse(month + '-' + date + '-' + year),//date
          now_time = Date.parse(new Date()),//
          dates = (new Date(year, month, 0)).getDate();//
      if (time > now_time || date > dates) {
        callback(new Error(''));
      }
      //
      var c = new Array(7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2);  //
      var b = new Array('1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2'); //
      var id_array = value.split("");
      var sum = 0;
      for (var k = 0; k < 17; k++) {
        sum += parseInt(id_array[k]) * parseInt(c[k]);
      }
      if (id_array[17].toUpperCase() != b[sum % 11].toUpperCase()) {
        callback(new Error(''));
      }
      callback();
    }
    return {
      kcdm:[],
      param: "",  // 
      //
      form:{
        name:"",
        sex:"",
        sfzh:"",
        ticket:"",
        tele:"",
        email:"",
        zycc:"",
        zymc:"",
        kcdm:[],
      },
      steps:"1",
      //
      rules:{
        name:[
          {required:true,message:"",trigger:"blur"},
          { pattern: /^[\u4E00-\u9FA5]+$/i,message: '',trigger:["blur","change"] },
        ],
        sex:[
          {required:true,message:"",trigger:"blur"}
        ],
        sfzh:[
          {required:true,validator: validateIdNo,trigger:"blur"}
        ],
        ticket:[
          {required:true,message:"",trigger:"blur"},
          { pattern: /^[\u4e00-\u9fa5a-zA-Z0-9]+$/,message: '',trigger:["blur","change"] },
          { pattern: new RegExp(/^\S*$/, 'g'), message: '',trigger:["blur","change"] }

        ],
        tele:[
          { pattern: new RegExp(/^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/,'g'),
            message: '',trigger:["blur","change"]}
        ],
        email:[
          {type:"email",message:"",trigger:"blur"},
          {required:true,message:"",trigger:["blur","change"] }
        ],
        zycc:[
          {required:true,message:"",trigger:"blur"}
        ],
        zymc:[
          {required:true,message:"",trigger:"blur"}
        ],
      },
      options: [
        {
          value: '',
          label: '',
        },
        {
          value: '',
          label: '',
        },
      ],
      fileList: [],
      options_zymc: [],
      Height:0,
    }
  },
  created() {
    // 
    this.$public.tokenlogin2(this.$cookies,this.axios,this.$store,this.$router,this.$message)
  },
  mounted(){      //
    this.GetCode();
    this.JudgeRes();
    this.$forceUpdate()         //
  },
  updated() {
    if( this.steps !== '1' ){     //
      this.$confirm('?', '', {
        confirmButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$router.push("/success/registerstatus");
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
  },
  methods: {
    //
    goIndex(){
      this.$router.push("/success/successindex");
    },
    //
    getInfo(){
      let params = new URLSearchParams()
      params.append("token",this.$cookies.get("token"))
      this.axios.post("personinfo",
          params
      ).then((response) => {
        //console.log(response.data)
        //console.log(response.data['msg'])

        if( response.data['msg'] === "ok"){
          //
          this.form.name = response.data['name']
          this.form.sex = response.data['sex']
          this.form.ticket = response.data['ticket']
          this.form.tele = response.data['tele']
          this.form.email = response.data['email']
          this.form.sfzh = response.data['sfzh']
          this.$forceUpdate()         //

        }
      })
    },
    //
    /*Buttonstate(){
      let param = new URLSearchParams();
      param.append("token",this.$cookies.get("token"))
      this.axios.post("signupres",
          param
      ).then((response) => {
        //console.log(response.data)
        //console.log(response.data['msg'])
        this.$forceUpdate()         //
        if( response.data['msg'] === "ok"){
          //  res01-12
          if( response.data['res'] === "2" ){         //
            this.$forceUpdate()
            //console.log("help")

            this.steps = "1";
          }else if( response.data['res'] === "0"  ){  //
            this.steps = "2";
          }else if( response.data['res'] === "1" || response.data['res'] === "-1"  ){     //
            this.steps = "3";
          }
        }
      })
    },*/
    GetCode(){
      //console.log("" )
      let param = new URLSearchParams();
      param.append("token",this.$cookies.get("token"))
      this.axios.post("showcode",
          param
      ).then((response) => {
        //console.log(response.data)
        if (response.data['msg'] === "ok") {
          this.kcdm = response.data["course"]
        }
      })
    },
    Change(){
      this.GetCode();
      this.form.zymc = ""
      if( this.form.zycc === "" ){
        this.options_zymc = [
          {
            value: '',
            label: '',
          },
          {
            value: '',
            label: '',
          },
        ]
      }else if( this.form.zycc === "" ) {
        this.options_zymc = [
        {
          value: '',
          label: '',
        },
        { value: '',
            label: '',
        },
      ]
      }
    },
    Submit() {
      let error = 0;
      this.$refs.signup.validate((valid) => {
        if (!valid) {
          this.$message({
            type: 'error',
            message: ''
          });
          error = 1;
          return;
        } else {
          //
        }
      });
      if( error == 1 )  return;


      /*if( this.fileList.length == 0 ){
        this.$message({
          type: 'error',
          message: ''
        });
        return
      }*/

      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$refs.upload.submit();
        //console.log(this.param)
        this.param.append("token",this.$cookies.get("token"))
        this.param.append('name',this.form.name);
        this.param.append('sex',this.form.sex);
        this.param.append('sfzh',this.form.sfzh);
        this.param.append('ticket',this.form.ticket);
        this.param.append('tele',this.form.tele);
        this.param.append('email',this.form.email);
        this.param.append('zycc',this.form.zycc);
        this.param.append('zymc',this.form.zymc);
        this.param.append('kcdm',this.form.kcdm);
        //    
        let configs = {
          headers: { "Content-Type": "multipart/form-data" }
        };

        this.axios.post("upload",
            this.param,configs
        ).then((response) => {
          //console.log(response.data)
          if (response.data['msg'] === "ok") {
            this.$forceUpdate()         //
          }
        })
        this.$message({
          type: 'success',
          message: '!'
        });
        this.JudgeRes()
        this.$forceUpdate()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    ///
    goHome() {
      this.$router.push("/index");
    },
    //
    goRegister() {
      this.$router.push("/register");
    },
    //
    goAdminLogin() {
      this.$router.push("/adminlogin");
    },
    //
    goForget() {
      this.$router.push("/forget");
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    beforeUpload(file) {
      this.param = new FormData();
      this.fileList.push(file);
      let images = [...this.fileList];
      //console.log("images", images);

      //this.param.append("file1", images);

      images.forEach((img) => {
        this.param.append("file", img); // 
      });
      return false;
    },
    //
    JudgeRes(){
      let param = new URLSearchParams();
      param.append("token",this.$cookies.get("token"))
      this.axios.post("signupres",
          param
      ).then((response) => {
        //console.log(response.data)
        //console.log(response.data['msg'])

        if( response.data['msg'] === "ok"){
          //  res01-12
          if( response.data['res'] === "2" ){      //
            this.steps = "1";
            this.$forceUpdate()
            this.getInfo()
          }else if( response.data['res'] === "0"  ){     //
            this.steps = "2";
          }else if( response.data['res'] === "1" || response.data['res'] === "-1"  ){     //
            this.steps = "3";
          }
          this.$forceUpdate()         //
        }
      })
    },
  }
}
</script>


<style lang="less" scoped>

/**/
.table{
  margin-left: 15px;
  margin-top:10px;
  width: 600px;
  font-size:14px;
  line-height:34px;
}
.sontable{
  margin-top:10px;
  margin-left:30px;
}
.el-card{
  width: 270px;
  height: 320px;
  margin-top:100px;
  margin-left:150px;
  span{
    font-weight: bolder;
    font-size:17px;
  }
  p{
    margin-top:20px;
    font-size:14px;
    color:#424242;
    line-height:34px;
  }
}
.el-input{
  width:240px;
}
</style>